import React, { memo, useRef, useEffect, useCallback, useState} from 'react';
import ZObjectContextMenus from "components/zobject/ZObjectContextMenus.js";
import ZObjectTooltip from "components/zobject/ZObjectTooltip.js";
import {useEdgeAdjuster} from "components/windows/adjustEdgeSize.js";
import {segmentAllCables} from "messaging/cables/segmentCables.js"
import {useDoubleClick} from "components/hooks/useDoubleClick.js";
import SubPatches from 'components/subpatch/SubPatches.js';
import {BANG, NUMBER} from 'zobject/data-types.js';
import ZObjectComponent from "components/zobject/ZObjectComponent.js";
import ZMessageComponent from "components/zobject/ZMessageComponent.js";
import StyleSetter from './StyleSetter.js';
import {useModuleDrop} from './useModuleDrop.js';
import PulledOverview from 'components/pulled/PulledOverview.js';
import ModuleSys5 from "components/system5/ModuleSys5.js";
import TrackMidiEffects from 'components/pulled/TrackMidiEffects.js';
import GridTab from 'components/pulled/GridTab.js';
import {useContainerCoords} from 'components/hooks/useContainerCoords.js';
import {useSelection} from "components/windows/useSelection.js";
import {
    setSelectedZObject, setModuleSize, setSelectedPulledElements} from 'actions/index.js';
import PulledBackground from './PulledBackground.js';
import PadEffects from 'components/pulled/PadEffects.js';
import GlobalEdges from 'components/pulled/GlobalEdges.js';
import PulledSequencer from 'components/pulled/PulledSequencer.js';
import PulledPad from 'components/pulled/PulledPad.js';
import PadsContainer from "components/pads/PadsContainer.js";
import {useWindows} from "components/windows/useWindows.js";
import {useArrangeWindows} from "components/windows/useArrangeWindows.js";
import {midiEffectOptions, initializeMidiEffect}  from 'components/effects/constants.js';
import {useContextMenu} from "components/dropdowns/useContextMenu.js";
import {setPulledEffects} from 'actions/index.js';
import ContextMenu from "components/dropdowns/ContextMenu.js";
import MenuItem from "components/dropdowns/MenuItem.js";
import PulledEffects from "components/pulled/PulledEffects.js";
import {connect} from 'react-redux';
import '../../ExploreGrid.scss';

const ModuleSpatialView = memo(({
    dropdownPosRef,
    isExplore,
    isLoading,
    presentationMode,
    doneLoading,
    innerRef,
    children,
    edgesRef,
    setSelection,
    setScale,
    setSize,
    showEffects,
    pulledEffects,
    setPulledEffects,
    setSelectedPulledElements,
    toggled,
    setSelectedTrack,
    subSequence,
    collapsed,
    zobjects,
    setSelectedZObject,
    selection,
    scale,
    elements,
    editorRef,
}) => {
    // const {onContextMenu, dropdownPos, setDropdowPos} = useContextMenu({ref: innerRef});
    const {sizeRef, ordering, setOrdering} = useWindows({ref: innerRef});

    useEdgeAdjuster(elements, subSequence, subSequence.sequencerEngine, sizeRef, setSize);

    //const {arrangeWindows} = useArrangeWindows({ref, elements, subSequence});

    let {getXY} = useContainerCoords({containerRef: innerRef, scale});
    let {getSelection} = useSelection({elements});

    const _onDoubleClick = useCallback((e) => {
        let {x,y} = getXY(e);
        e.stopPropagation();
        e.preventDefault();
        subSequence.createZObject(x,y);
    }, [subSequence]);

    let onDoubleClick = useDoubleClick(_onDoubleClick);

  
    let rows = [0,1,2,3,4];
    let cols = [0,1,2];
    let {tracks=[]} = subSequence;
    tracks = tracks.filter(x => isExplore || x.pad === undefined);

    const createZObject= useCallback((x,y,type) => {
        if (!x && !y) {
            x = dropdownPosRef.current.x;
            y = dropdownPosRef.current.y;
        }
        subSequence.createZObject(x,y, type);
    }, [subSequence]);

    const createZMessage = useCallback((x,y) => {
        if (!x && !y) {
            x = dropdownPosRef.current.x;
            y = dropdownPosRef.current.y;
        }
        subSequence.createZMessage(x,y);
    }, [subSequence]);

    const createZNumber = useCallback((x,y) => {
        if (!x && !y) {
            x = dropdownPosRef.current.x;
            y = dropdownPosRef.current.y;
        }
        subSequence.createZMessage(x,y, NUMBER);
    }, [subSequence]);

    const createButton = useCallback((x,y) => {
        if (!x && !y) {
            x = dropdownPosRef.current.x;
            y = dropdownPosRef.current.y;
        }
        subSequence.createZMessage(x,y, BANG);
    }, [subSequence]);

    const createToggle = useCallback((x,y) => {
        if (!x && !y) {
            x = dropdownPosRef.current.x;
            y = dropdownPosRef.current.y;
        }
        subSequence.createZObject(x,y, "toggle");
    }, [subSequence]);

    const createPad = useCallback((x,y) => {
        let pad = subSequence.createPad();
        if (!x && !y) {
            x = dropdownPosRef.current.x;
            y = dropdownPosRef.current.y;
        }
        pad.x = x;
        pad.y = y;
    }, [subSequence]);

    const onDrop = useModuleDrop({subSequence, createToggle, createPad, createZObject, createZMessage, createButton, createZNumber, getXY, setSelection});

    useEffect(() => {
        if (editorRef.current) {
            editorRef.current.addEventListener("drop", onDrop);
        } else {
            innerRef.current.addEventListener("drop", onDrop);
        }

        return () => {
            if (editorRef.current) {
                editorRef.current.removeEventListener("drop", onDrop);
            } else if (innerRef.current) {
                innerRef.current.removeEventListener("drop", onDrop);
            };
        }
    }, []);

    if (!subSequence || !subSequence.tracks || !doneLoading) {
        return [];
    }


    if (!zobjects) {
        zobjects = subSequence.zObjects;
    }

    let inner = (
        <div>
         <PulledEffects
            scale={scale}
            sizeRef={sizeRef}
            setSize={setSize}
            ordering={ordering}
            setOrdering={setOrdering}
            playhead={true}
            elements={elements}
            containerRef={innerRef}
            noWrapper={true}
            sequencerEngine={subSequence.sequencerEngine}
            subSequence={subSequence}/>
          <ZObjectTooltip subSequence={subSequence}/>
          <ZObjectContextMenus subSequence={subSequence}/>
          {!isExplore && zobjects.map(
              obj => <ZObjectComponent
                       key={obj.id}
                       scale={scale}
                       sequencerEngine={subSequence.sequencerEngine}
                       ordering={ordering}
                       setOrdering={setOrdering}
                       setSize={setSize}
                       sizeRef={sizeRef}
                       elements={elements}
                       containerRef={innerRef}
                       zobject={obj}/>)}

{!isExplore && subSequence.zMessages.map(
              obj => <ZMessageComponent
                       key={obj.id}
                       scale={scale}
                       sequencerEngine={subSequence.sequencerEngine}
                       ordering={ordering}
                       setOrdering={setOrdering}
                       setSize={setSize}
                       sizeRef={sizeRef}
                       elements={elements}
                       containerRef={innerRef}
                       zmessage={obj}/>)}

        {!isExplore && subSequence.tracks.map(
              track => 
                  <TrackMidiEffects
                    scale={scale}
                    ordering={ordering}
                    setOrdering={setOrdering}
                    setSize={setSize}
                    sizeRef={sizeRef}
                    elements={elements}
                    containerRef={innerRef}
                    track={track}/>)}
          {!isExplore && subSequence.zObjects.filter(x => x.name === 'pad')
           .filter(x => x.effect.pad)
           .map(x => x.effect.pad)
           .filter(pad => pad.effectRack)
           .map(
              pad => 
                  <PadEffects
                    scale={scale}
                    ordering={ordering}
                    setOrdering={setOrdering}
                    setSize={setSize}
                    sizeRef={sizeRef}
                    elements={elements}
                    containerRef={innerRef}
                    pad={pad}/>)}
          {!isExplore && subSequence.sequencers.map(
              sequencer =>
                  <PulledSequencer
                    ordering={ordering}
                    setOrdering={setOrdering}
                    scrollContainer={innerRef}
                    scale={scale}
                    setSize={setSize}
                    sizeRef={sizeRef}
                    elements={elements}
                    sequencer={sequencer}/>)}
        </div>);
    
    let className = collapsed ? "grids collapsed" : "grids not-collapsed";
    if (presentationMode) {
        className += " presentation-mode";
    }
    return (
        <div
          onClick={edgesRef && edgesRef.current ? () => 0 : onDoubleClick}
          className={className}>
          <GridTab subSequence={subSequence}/>
          <StyleSetter
            scale={scale}
            setScale={setScale}
            edgesRef={edgesRef}
            subSequence={subSequence}
            editorRef={editorRef}
            setSize={setSize}
            sizeRef={sizeRef}
            isExplore={isExplore}
            collapsed={collapsed}
            containerRef={innerRef}
            subSequence={subSequence}/>
          <div
            ref={innerRef} 
            className="grids-inner">
            {children}
            {collapsed ? <ModuleSys5 subSequence={subSequence} /> : inner}
          </div>
        </div>
    );
});

const mapStateToProps = (state, props) => {
    return {
        presentationMode: state.spatial.presentationMode,
        doneLoading: state.sequencer.completelyDoneLoading,
        pulledEffects: state.effects.pulledEffects,
        pulledSequencers: state.pads.sequencers,
        numTracks: props.subSequence.tracks && props.subSequence.tracks.length,
        collapsed: state.pads.modulesCollapsed[props.subSequence.id],
        zobjects: state.zobject.zobjects[props.subSequence.id],
        zmessages: state.zobject.zmessages[props.subSequence.id],
    };
};

const mapDispatchToProps = (dispatch, props) => {
    return {
        setPulledEffects: x => dispatch(setPulledEffects(x)),
        setSelectedPulledElements: x => dispatch(
            setSelectedPulledElements(x)),
        setSelectedZObject: x => dispatch(
            setSelectedZObject(x)),
        setSize: x => dispatch(
            setModuleSize(props.subSequence.id, x)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(ModuleSpatialView);
