import React, { useRef, useEffect, useCallback, useState} from 'react';
import {useWindows} from "components/windows/useWindows.js";
import ZObjectWindow from './ZObjectWindow.js';
import {connect} from 'react-redux';
import SubPatch from './SubPatch.js';

const SubPatches = ({subPatches, zobjects, containerRef, fullScreenZObject}) => { 
    const elements = useRef({});
    const {sizeRef, ordering, setOrdering} = useWindows();

    let toHide = [];
    for (let subpatch of subPatches) {
        let {zObject} = subpatch;
        if (subPatches.includes(zObject.subSequence)) {
            // then we want to disclude the parent one
            toHide.push(zObject.subSequence);
        }
    }
    return [
        zobjects.map(
            zobject =>
                <ZObjectWindow
                  zobject={zobject}
                  elements={elements}
                  sizeRef={sizeRef}
                  containerRef={containerRef}
                  setSize={() => 0}
                  ordering={ordering}
                  setOrdering={setOrdering}/>
        ),
        subPatches
            .filter(x => !fullScreenZObject || fullScreenZObject.effect !== x)
            .filter(
        x => !toHide.includes(x)).map(
            x => <SubPatch
                   key={x.id}
                   sizeRef={sizeRef}
                   ordering={ordering}
                   setOrdering={setOrdering}
                   elements={elements}
                   subPatches={subPatches}
                   subpatch={x}
                   containerRef={containerRef}/>)
        ];
};

const mapStateToProps = (state, props) => {
  return {
      subPatches: state.zobject.viewableSubPatches,
      zobjects: state.zobject.viewableZObjects,
      fullScreenZObject: state.zobject.fullScreenZObject
  };

};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubPatches);
