import React, { useRef, useEffect, useCallback, useState} from 'react';
import {
    setEditorPadding,
    setSelectedZObject,
    setModuleSize,
    setSelectedPulledElements} from 'actions/index.js';
import {connect} from 'react-redux';

window.scr = 45;
const MIN_SCALE = 0.30;

const StyleSetter = ({
    editorRef,
    setEditorPadding,
    uxLocked,
    edgesPadding,
    editorPadding,
    showingSubpatch,
    sidebarsShowing,
    mainScrollRef,
    size,
    setSize,
    sizeRef,
    containerRef,
    isExplore,
    collapsed,
    showPadSequencer,
    edgesRef,
    edgesSize,
}) => { 
    let [styleSize, setStyleSize] = useState(size);
    let lastZoom = useRef(undefined);
    let startZoomPosition = useRef(undefined);
    let startZoomScroll = useRef(undefined);
    let mouseRef = useRef(undefined);

    useEffect(() => {
        if (editorRef.current) {
            if (showPadSequencer) {
                editorRef.current.style.paddingTop = '0px';
                editorRef.current.style.paddingLeft = '0px';
            } else {
                editorRef.current.style.paddingTop= editorPadding.y + 'px';
                editorRef.current.style.paddingLeft= editorPadding.x + 'px';
            }
        }
    }, [showPadSequencer, editorPadding]);

    useEffect(() => {
        if (edgesRef.current) {
            edgesRef.current.style.width = edgesSize.width +'px';
            edgesRef.current.style.height = edgesSize.height + 'px';
            edgesRef.current.style.minWidth = edgesSize.width +'px';
            edgesRef.current.style.minHeight = edgesSize.height + 'px';
            edgesRef.current.style.left = edgesPadding.x;
            edgesRef.current.style.top = edgesPadding.y;
        }
    }, [edgesPadding, edgesSize]);

    useEffect(() => {
        setStyleSize(size);
    }, [size, setStyleSize]);

    useEffect(() => {
        if (!mainScrollRef || !mainScrollRef.current) {
            return () => 0;
        }
        mainScrollRef.current.addEventListener("mousewheel", onWheel, {passive: false});
        mainScrollRef.current.addEventListener("mousemove", onMouseMove);;
        return () => {
            if (mainScrollRef && mainScrollRef.current) {
                mainScrollRef.current.removeEventListener("mousewheel", onWheel, {passive: false});
                mainScrollRef.current.removeEventListener("mousemove", onMouseMove);;
            }
        };
    }, [setSize,
        uxLocked,
        editorPadding,
        showingSubpatch, mainScrollRef, sidebarsShowing, styleSize, setStyleSize]);

    const onMouseMove = (e) => {
        if (mouseRef.current) {
            mouseRef.current.x = e.clientX;
            mouseRef.current.y = e.clientY;
        } else {
            mouseRef.current = {
                x: e.clientX,
                y: e.clientY,
            };
        }
    };

    const onWheel = useCallback((e) => {
        if (window.killScrolling || uxLocked) {
            e.preventDefault();
            return;
        }

        if (!mainScrollRef || !mainScrollRef.current) {
            return false;
        }

        e.preventDefault();

        if (!handleScale(e)) {
            return;
        }

        let {scrollTop, scrollLeft} = mainScrollRef.current;

        let {deltaX, deltaY} = e;

        /*
        if (Math.abs(deltaX) < 2) {
            deltaX *= 0.5;
        }
        if (Math.abs(deltaY) < 2) {
            deltaY *= 0.5;
        }
        */

        let x = scrollLeft + deltaX;
        let y = scrollTop + deltaY;


        if (!size) {
            // return false;
        }

        let sx = sidebarsShowing ? 2*240 : 0;
        let sy = sidebarsShowing ? 45 : 0;

        let _width = styleSize ? styleSize.width : 400;
        let _height = styleSize ? styleSize.height : 400;

        size = {
            width: Math.max(_width, window.innerWidth - sx),
            height: Math.max(_height, window.innerHeight - sy)
        };
                
        if (size.width < 200 || size.height < 200) {
            return;
        }
        let _size = {width: deltaX + size.width, height: deltaY + size.height};

        if (x < 0 || y < 0) {
            let _x= editorPadding.x - deltaX;
            let _y= editorPadding.y - deltaY;
            setEditorPadding(_x, _y);
        }

        containerRef.current.style.width = _size.width+window.scr+'px';
        containerRef.current.style.height = _size.height+window.scr+'px';
        setStyleSize(_size);
        sizeRef.current = _size;
        mainScrollRef.current.scrollTo(x,y);
        window.lastScrollTime = new Date().getTime();
        return false;
    },[
        editorPadding,
        uxLocked,
        styleSize,
        setStyleSize,
        showingSubpatch,
        sidebarsShowing,
        mainScrollRef
      ]);

    const handleScale = useCallback((e) => {
       let {deltaX, deltaY, wheelDeltaX, wheelDeltaY} = e;
        if (Math.max(Math.abs(deltaX), Math.abs(deltaY)) < 20 && Math.abs(wheelDeltaY) > 110)  {
            let scale = window._scale || 1;
            let _scale=1;
            if (wheelDeltaY > 0) {
                _scale =  scale * wheelDeltaY / 117.0;
            } else {
                _scale = Math.abs(scale / (wheelDeltaY / 117.0));
            }
            window._scale = Math.max(MIN_SCALE, Math.min(1, _scale));
            if (!lastZoom.current || new Date().getTime() - lastZoom.current > 500) {
                let rect = containerRef.current.getBoundingClientRect();
                let {scrollLeft, scrollTop} = mainScrollRef.current;
                startZoomPosition.current = {
                    x: ((mouseRef.current.x - rect.left)) / scale,
                    y: ((mouseRef.current.y - rect.top)) / scale,
                };
                startZoomScroll.current = {
                    x: scrollLeft / scale,
                    y: scrollTop / scale
                };
            }

            // ensure that the scroll position matches that x and y
            let x = startZoomPosition.current.x;
            let y = startZoomPosition.current.y;
            let sx = startZoomScroll.current.x;
            let sy = startZoomScroll.current.y;

            // the global (including scroll) position of (goal) cursor
            // i.e. where we want the cursor to be after scrolling
            let X = (x + sx) / window._scale;
            let Y = (y + sy) / window._scale;

            lastZoom.current = new Date().getTime();

            if (window._scale === 1) {
                containerRef.current.style.transform = "none";
            } else {
                containerRef.current.style.transform = "scale(" + window._scale + ")";
            }

            let {scrollLeft, scrollTop}  = mainScrollRef.current;
            /*
            setTimeout(() => {
                mainScrollRef.current.scrollTo(X+X/window._scale,Y+Y/window._scale);
            }, 5);
            */
            return false;
        } else {
            return true;
        }
    }, [mainScrollRef]);

    useEffect(() => {
        if (isExplore || collapsed) {
            containerRef.current.style = {};
        } else if (styleSize) {
            containerRef.current.style.width = styleSize.width+window.scr+'px';
            containerRef.current.style.height = styleSize.height+window.scr+'px';
        }
    }, [collapsed, isExplore, styleSize]);


    return '';
};

const mapStateToProps = (state, props) => {
    return {
        size: state.windows.moduleSizes[props.subSequence.id],
        mainScrollRef: state.spatial.mainScrollRef,
        sidebarsShowing: state.spatial.sidebarsShowing,
        showingSubpatch: state.zobject.viewableSubPatches.length > 0,
        editorPadding: state.spatial.editorPadding,
        edgesPadding: state.spatial.edgesPadding,
        edgesSize: state.spatial.edgesSize,
        showPadSequencer: state.spatial.showPadSequencer,
        uxLocked: state.zobject.uxLocked
    };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
      setEditorPadding: (x,y)=> dispatch(
          setEditorPadding(x,y)),
        setSize: x => dispatch(
            setModuleSize(props.subSequence.id, x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(StyleSetter);
