import React, { useRef, useEffect, useCallback, useState} from 'react';
import ZObjectComponent from "components/zobject/ZObjectComponent.js";
import {connect} from 'react-redux';
import {useWindowManager} from "components/windows/useWindowManager.js";
import {
    setViewableZObjects
} from 'actions/index.js';

const ZObjectWindow = ({
    containerRef,
    setSize,
    sizeRef,
    setViewableZObjects,
    zobject,
    ordering,
    setOrdering,
    elements,
    windowPosition
}) => { 
    const ref = useRef();
    const {windowScale, setWindowScale, onMouseDown, onMouseUp, style, startResizing} = useWindowManager({
        sequencerEngine: zobject.sequencerEngine,
        basicContainerRef: containerRef,
        ref,
        ordering,
        setOrdering,
        element: zobject.window,
        setSize,
        sizeRef,
        elements,
        windowPosition
    });

    let _style = {
    };

    if (windowPosition) {
        _style.left = windowPosition.x;
        _style.top = windowPosition.y;
    }

    const closeWindow = useCallback(() => {
        let {viewableZObjects} = zobject.state.zobject;
        setViewableZObjects(viewableZObjects.filter(
            x => x !== zobject));
    }, [zobject]);

    return (
        <div className="zobject-window" style={style} ref={ref}>
          <div
            onMouseDown={onMouseDown}
            onMouseUp={onMouseUp}
            className="zobject-header">
            <div onClick={closeWindow} className="delete-btn">
              x
            </div>
          </div>
          <ZObjectComponent
            scale={1}
            elements={elements}
            sequencerEngine={zobject.sequencerEngine}
            ordering={ordering}
            setOrdering={setOrdering}
            isStatic={true}
            customMode={false}
            setSize={setSize}
            sizeRef={sizeRef}
            containerRef={ref}
            zobject={zobject}
          />
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
      windowPosition: state.windows.windowPositions[props.zobject.window.id]
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setViewableZObjects: x => dispatch(setViewableZObjects(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZObjectWindow);
