import React, { useEffect, useCallback, useState} from 'react';
import AutomationPaths from './AutomationPaths.js';
import HigherOrderHeader from "components/system5/HigherOrderHeader.js";
import Sidebar from './Sidebar.js';
import Playhead from './Playhead.js';
import HigherOrderStepComponent from './HigherOrderStepComponent.js';
import {
    setShowHigherOrderSequencer,
    setHigherOrderStepsSelected} from 'actions/index.js';
import {connect} from 'react-redux';
import HigherColumn from "components/system5/HigherColumn.js";
import {useKeys} from './useKeys.js';
import '../../HigherOrder.scss';

const HigherOrderComponent = ({setShowHigherOrderSequencer, sequencerEngine, selected, setSelected, show}) => { 
    let {higherOrderSequencer} = sequencerEngine;
    let {matrix} = higherOrderSequencer;
    let [dragging, setDragging] = useState(undefined);
    let [selection, setSelection] = useState(undefined);
    let [moving, setMoving] = useState(undefined);

    useEffect(() => {
        window.addEventListener("mouseup", onMouseUp);
        return () => {
            window.removeEventListener("mouseup", onMouseUp);
        };
    }, [setDragging]);

    useEffect(() => {
        return () => setSelected(undefined);
    }, []);

    useKeys({higherOrderSequencer, setSelected, selected});

    const onMouseUp = useCallback(() => {
        setDragging(undefined);
        setMoving(false);
    }, [setDragging]);

    const onMouseMove = useCallback(() => {
        if (dragging) {
            setMoving(true);
        }
    }, [dragging, setMoving]);

    if (!show) {
        return (<HigherOrderHeader/>);
    }
    let svgClass  = selection ? "shape-container selection" : "shape-container";
    if (moving) {
        svgClass += " moving";
    }
    return (
        <div
          onMouseMove={onMouseMove}
          className="higher-order-component">
          <div className="higher-inner">
            <Playhead sequencerEngine={sequencerEngine}/>
            {matrix.map(
                (col, x) =>
                    <HigherColumn
                      setSelection={setSelection}
                      selection={selection}
                      setSelected={setSelected}
                      selected={selected}
                      setDragging={setDragging}
                      dragging={dragging} x={x} higherOrderSequencer={higherOrderSequencer}/>
            )}
            <svg className={svgClass}>
              {higherOrderSequencer.steps.map(
                  stepData => 
                      <HigherOrderStepComponent
                        setSelected={setSelected}
                        setSelection={setSelection}
                        isSelected={selected && selected.includes(stepData)}
                        dragging={dragging}
                        x={stepData.x} y={stepData.y}
                        orientation={stepData.orientation}
                        isDragging={dragging && dragging.x === stepData.x && dragging.y === stepData.y}
                        size={stepData.size}
                        setDragging={setDragging}
                        color={stepData.module && stepData.module.color}
                        stepData={stepData}
                        higherOrderSequencer={higherOrderSequencer}/>)}
              {higherOrderSequencer.automationPaths &&
               <AutomationPaths automationPaths={higherOrderSequencer.automationPaths}/>}
            </svg>
          </div>
          <Sidebar higherOrderSequencer={higherOrderSequencer} selected={selected}/>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
      matrix: state.higherOrder.matrix,
      selected: state.higherOrder.selectedSteps,
      show: state.higherOrder.show
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setShowHigherOrderSequencer: (x) => dispatch(setShowHigherOrderSequencer(x)),
      setSelected: (x) => dispatch(setHigherOrderStepsSelected(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HigherOrderComponent);
