import React, { useEffect, useCallback, useState} from 'react';
import EffectParameterSlider from "components/effects/EffectParameterSlider.js";
import {SAMPLER} from 'constants/track-types.js';
import CheckboxParameter from "components/effects/CheckboxParameter.js";
import {connect} from 'react-redux';
import ChokesAndMatches from 'components/sidebar/ChokesAndMatches.js';
import {makeInstrumentRack} from "components/v2/trackRightClickActions.js"

const TrackQuickAux = ({track}) => { 
    let [show, setShow] = useState(false);
    return (
        <div className="sidebar-section">
          <div onClick={() => setShow(true)} className={show ? "label show-aux showing" : "label show-aux"}>
            Aux
        <div className="plus-minus">
          {show && <div
            onClick={(e) => {
                e.stopPropagation();
                setShow(false);
            }}
                     className="plus minus">-</div>}
          <div className="plus">+</div>
        </div>
          </div>
          {show && <div>
          <div className="label">
            Chokes
          </div>
          <ChokesAndMatches sequencerEngine={track.sequencerEngine} track={track}/>
          <CheckboxParameter name="Skip Module Midi" parameter={track.skipMidiMasterParameter}/>
          {track.getTrackType() === SAMPLER &&
           <div onClick={() => makeInstrumentRack(track)} className="convert-to-rack pushable">
             Convert to Sampled Instrument
           </div>}
          {track.getTrackType() === SAMPLER &&
           track.getSamplers()[0] &&
           track.getSamplers()[0].detuneParameter &&
           <EffectParameterSlider parameter={track.getSamplers()[0].detuneParameter}
           name="Detune"
           min={-1000}
           max={1000}
                                  step={0.1}
           sequencerEngine={track.sequencerEngine}
           />}
                   </div>}
        </div>
    );
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TrackQuickAux);
