import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {doc} from 'zobject/definitions/docs.js';
import './operation.scss';

const OperationDocs = ({zobject}) => { 
    let _doc = doc(zobject.name);
    if (!_doc.operationDocs) {
        return '';
    }
    let operationNames = _doc.operationDocs();
    return (<div className="zobject-operations">
              <div className="label">
                Operations
              </div>
            {operationNames.map(
                name =>
                <div className="operation-type">
                  <div className="name-and-description">
                    <div className="operation-name">
                      {name}
                    </div>
                    <div className="operation-description">
                      {_doc.operationDocs(name).description}
                    </div>
                  </div>
                  <div className="operation-args">
                    {Object.keys(_doc.operationDocs(name).params).map(
                        paramName => <div className="operation-param">
                                       <div className="dot"/>
                                       <div className="param-name">
                                         {paramName}
                                       </div>
                                       <div className="param-description">
                                         {_doc.operationDocs(name).params[paramName]}
                                       </div>
                         </div>)}
                  </div>
                </div>)}
            </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(OperationDocs);
