import React, { useEffect, useCallback, useState} from 'react';
import PlaneTools from './PlaneTools.js';
import {connect} from 'react-redux';
import './effects.scss';

const EffectTools = ({effect}) => { 
    let context = '';
    switch (effect.name) {
    case "Plane Parameters":
        context = (<PlaneTools effect={effect}/>);
        break;
    }
    return (
        <div className="effect-tools">
          {effect.documentation &&
           <div className="effect-documentation">
             {effect.documentation}
           </div>}
          {context}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(EffectTools);
