import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import ParameterKnob from "components/knobs/ParameterKnob.js";


const names = ['A', 'B', 'C', 'D'];
const TrackQuickSends = ({track}) => { 
    let sends = track.sequencerEngine.sends.sends;
    return (
       <div className="track-quick-sends">
         <div className="label">
           Sends
         </div>
        <div className="send-knobs">
          {track.sendNodeVolumeParameters.filter(
              (x,i) => sends[i] && !sends[i].pad).map(
            (send, i) =>
                <div className="send-knob">
                  <ParameterKnob
                    sequencerEngine={track.sequencerEngine}
                    min={-40} parameter={send}/>
                  <div className="send-name">
                    {names[i]}
                  </div>
                </div>)}
        </div>
       </div>
    );
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TrackQuickSends);
