import React, { useRef, useEffect, useCallback, useState} from 'react';
import {
    setStepParameterSelected,
    setParametersToRecord,
} from 'actions/index.js';
import {connect} from 'react-redux';

const ParamValue = ({
    setStepParameterSelected,
    lastPlayedStepData,
    isRecording,
    trackStep,
    track,
    parameters,
    rounded=false,
    setParametersToRecord,
    isSelected
}) => { 
    let ref = useRef(undefined);
    let [hover, setHover] = useState(false);
    const getParameterValue = useCallback((p) => {
        if (!p) {
            return 0;
        }
        if (p.get() === null) {
            return track.trackData[p.name.toLowerCase() + 'Parameter'].get();
        }
        return p.get();
    }, [track]);

    let [value, setValue] = useState(getParameterValue(parameters[0], track));
    let [editing, setEditing] = useState(false);

    useEffect(() => {
        window.addEventListener("mouseup", onMouseUp);
        window.addEventListener("mousemove", onMouseMove);

        return () => {
            window.removeEventListener("mouseup", onMouseUp);
            window.removeEventListener("mousemove", onMouseMove);
        };
    }, [parameters, setEditing, editing, setValue, isSelected]);

    useEffect(() => {
        setValue(getParameterValue(parameters[0]), track);
    }, [getParameterValue(parameters[0], track), setValue, track]);

    const onMouseMove = useCallback(e => {
        if (editing) {
            let {y, number} = editing;
            let {pageY} = e;
            let distY = y - pageY;
            let ratio = Math.pow(distY / y, 2);
            if (distY < 0) {
                ratio *= -1;
            }
            let parameter = parameters[0];
            let max = parameter.max;
            let min = parameter.min;
            if (parameter.name === "Duration") {
                max = 2;
                min = 0.01;
            } else if (parameter.name === "Velocity") {
                min = 0.001;
            }
            let _number = ratio*(max - (min+0.001)) + number;
            _number = Math.max(min, Math.min(max, _number));
            if (rounded) {
                _number = Math.round(_number);
            }
            if (_number !== getParameterValue(parameter, track)) {
                parameters.forEach(p => p.set(_number));
                setValue(_number);
            }
            setParametersToRecord(parameters);
        }
    }, [parameters, isRecording, setEditing, editing, setValue, track]);

    const onMouseUp = useCallback(e => {
        if (editing) {
            setEditing(false);
        }

        setParametersToRecord([]);
    }, [setEditing, editing]);

    let className = "track-parameter";
    if (isSelected) {
        className += " selected";
    }
    return (
        <div onMouseOver={() => setHover(true)} onMouseLeave={() => setHover(false)} className={className}>
          {(hover || editing) && isRecording &&
           <div className="recording-indicator">
             <div
               style={{width: (110 * (trackStep % track.pattern.length()) / track.pattern.length()) + 'px'}}
               className={editing ? "recording-progress editing" : "recording-progress"}>
             </div>
           </div>}
        <div
          ref={ref}
            onMouseDown={(e) => {
                setEditing({number: getParameterValue(parameters[0], track), y: e.pageY});
                setStepParameterSelected(parameters[0].name);
            }}
          className="param-name">
          {parameters[0] ? parameters[0].name : ''}
        </div>
          <div
            onMouseDown={(e) => {
                setEditing({number: getParameterValue(parameters[0], track), y: e.pageY});
                setStepParameterSelected(parameters[0].name);
            }}
            className="param-value">
            {hover && isRecording && !editing && lastPlayedStepData && parameters[0] ?
             round(lastPlayedStepData[parameters[0].name.toLowerCase() + "Parameter"].get()
                ) : round(value)}
          </div>
        </div>
    );
}

const round = (value) => {
    return Math.round(value*100)/100;
};

const mapStateToProps = (state, props) => {
    let isRecording= props.track.sequencerEngine.isRecording;
    return {
        isRecording,
        lastPlayedStepData: props.track.lastPlayedStepData,
        trackStep: isRecording ? props.track.sequencerEngine.trackStep : 0
    };
};

const mapDispatchToProps = (dispatch) => {
  return {
        setParametersToRecord: params => dispatch(setParametersToRecord(params)),

      setStepParameterSelected: params => dispatch(setStepParameterSelected(params)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ParamValue);
