import React , {useEffect} from 'react';
import {GO_BACK_HOME} from 'constants/contextual-help.js';
import Documented from "components/help/Documented.js";
import {setSelectedZObject} from 'actions/index.js';
import {connect} from 'react-redux';
import {TRACK} from 'constants/object-types.js';
import {
    savePreset,
} from 'components/v2/trackRightClickActions.js';

const Navigator =  ({streamSize, zobject, options, setOption, option, showSave=false, setSelectedZObject}) => {
    useEffect(() => {
        if (!options.includes(option)) {
            setOption(options[0]);
        }
    }, [options, setOption, option]);
    let auditionName = zobject && zobject.getObjectType && zobject.getObjectType() === TRACK ?
        "Sounds" : "Presets";
    
    return (
        <div className="sidebar-navigator">
          <Documented
            element={GO_BACK_HOME}
            onClick={() => setSelectedZObject(undefined)}
            className="back-arrow-container pushable">
            <div className="arrow-left"/>
          </Documented>
          {options
           .map(
              _option =>
              <div
                onClick={() => setOption(_option)}
                className={_option === option ? "selected option " + _option+'-option': "option " + _option+'-'+"option"}>
                {_option === "Audition" ? auditionName : _option}
                {streamSize > 0 && _option === "Audition" && <span className="audition-size">{streamSize}</span>}
              </div>)}
          {showSave && <img className="save-preset-icon" onClick={() => savePreset(zobject)} src="/save-preset-floppy.svg"/>}


        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
      streamSize: props.zobject && props.zobject.samplesStream &&
          props.zobject.samplesStream.length
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSelectedZObject: x => dispatch(setSelectedZObject(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Navigator);
