import React, { useEffect, useCallback, useState} from 'react';
import TrackThumbnail from "components/TrackThumbnail.js";
import {getEffectTrack} from "utils/getEffectTrack.js";
import {AUDIO_FX, MIDI_FX, LIVE_FX} from 'constants/effect-types.js';
import {connect} from 'react-redux';

const EffectInformation = ({effect, _locked}) => { 
    let track = getEffectTrack(effect);

    let [locked, setLocked] = useState(_locked);

    const toggleLock = useCallback(() => {
        effect.lock = !locked;
        setLocked(!locked);
    }, [effect, locked, setLocked ]);

    useEffect(() => {
        setLocked(_locked);
    }, [_locked, setLocked]);

    return (
        <div className="effect-information object-information">
          <div className="effect-thumbnail">
            {effect.getEffectType() === AUDIO_FX ? "Audio FX" :
             effect.getEffectType() === MIDI_FX ? "MIDI FX" :
             "Live FX"}
          </div>
          <div className="effect-name">{effect.name}</div>
          <img
            onClick={toggleLock}
            src={locked ? "/closed-padlock.png" :"/opened-padlock.png"} className="effect-lock"/>
          {track && <TrackThumbnail hasInner={true} track={track}/>}
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
      _locked: props.effect.locked
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(EffectInformation);
