import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {ChromePicker} from 'react-color';

const SubPatchColors = ({subpatch}) => { 
    let [fill, setFill] = useState({hex: subpatch.fillColor || "#24273a"});

    useEffect(() => {
        subpatch.fillColor = fill.hex;
        console.log('ssetting to ', fill.hex);
    }, [fill]);

    let [showing, setShowing] = useState(undefined);
    return (
        <div className="subpatch-colors">
          <div
            onClick={() => setShowing(showing ? undefined : "fill")}
            style={{backgroundColor: fill.hex}} className="color-btn"/>
          {showing === "fill" &&
           <ChromePicker color={fill} onChange={setFill}></ChromePicker>}
        </div>
    );
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubPatchColors);
