import React, { useEffect, useCallback, useState} from 'react';
import ZObjectTools from './zobject/ZObjectTools.js';
import PadToolsSidebar from './pad/PadToolsSidebar.js';
import {EFFECT, ZOBJECT, ZMESSAGE, PAD, TRACK} from 'constants/object-types.js';
import {connect} from 'react-redux';
import TrackToolsSidebar from './track/TrackToolsSidebar.js';
import ZMessageTools from './zobject/ZMessageTools.js';
import EffectTools from './effects/EffectTools.js';

const ToolsSidebar = ({object, sequencerEngine}) => { 
    let inner = '';
    if (!object.getObjectType) {
        return '';
    }
    switch (object.getObjectType()) {
    case TRACK:
        inner = <TrackToolsSidebar track={object}/>;
        break;
    case PAD:
        inner = <PadToolsSidebar pad={object}/>;
        break;
    case ZMESSAGE:
        inner = <ZMessageTools zmessage={object}/>;
        break;
    case ZOBJECT:
        inner = <ZObjectTools zobject={object}/>;
        break;
    case EFFECT:
        inner = <EffectTools effect={object}/>;
        break;
    }
    return (
        <div className="tools-sidebar">
          {inner}
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ToolsSidebar);
