import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import TrackInformation from "components/v3/sidebar/track/TrackInformation.js";
import ZObjectInformation from "components/v3/sidebar/zobject/ZObjectInformation.js";
import ZMessageInformation from "components/v3/sidebar/zobject/ZMessageInformation.js";
import PadInformation from "components/v3/sidebar/pad/PadInformation.js";
import EffectInformation from "components/v3/sidebar/effects/EffectInformation.js";
import {EFFECT, ZOBJECT, ZMESSAGE, PAD, TRACK} from 'constants/object-types.js';

const ObjectInformation = ({object}) => { 
    if (!object || !object.getObjectType) {
        return '';
    }

    if (object.pluginViewerFor) {
        object = object.pluginViewerFor;
    }
    if (object.getObjectType() === TRACK) {
        return (<TrackInformation track={object}/>);
    } else if (object.getObjectType() === PAD) {
        return (<PadInformation pad={object}/>);
    } else if (object.getObjectType() === ZOBJECT) {
        return (<ZObjectInformation zobject={object}/>);
    } else if (object.getObjectType() === ZMESSAGE) {
        return (<ZMessageInformation zmessage={object}/>);
    } else if (object.getObjectType() === EFFECT) {
        return (<EffectInformation effect={object}/>);
    }
    return (<div></div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ObjectInformation);
