import React, { useEffect, useCallback, useState} from 'react';
import RightSidebar from "components/v3/sidebar/RightSidebar.js"
import {connect} from 'react-redux';
import './subpatch-sidebar.scss';

const SubPatchSidebar = (props) => { 
    let [opened, setOpened] = useState(false);

    useEffect(() => {
        if (!props.selectedZObject) {
            setOpened(false);
        }
    }, [props.selectedZObject, setOpened]);
    return (
        <div
          style={{height: props.subpatch.height - 65}}
          className={opened ? "subpatch-sidebar opened" : "subpatch-sidebar"}>
        {!opened ?
         (props.selectedZObject &&
          props.selectedZObject.subSequence === props.subpatch && 
          <img className="pushable info-icon" onClick={() => setOpened(true)} src="/info-icon.svg"/>) :
         <RightSidebar seauencerEngine={props.sequencerEngine}/>}
        </div>
    );
}

const mapStateToProps = (state, props) => {
  return {
      height: props.subpatch.height,
      selectedZObject: state.zobject.selectedZObject
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubPatchSidebar);
