import React, { useEffect, useCallback, useState} from 'react';
import {isNumber} from 'utils/IsNumber.js';
import {connect} from 'react-redux';

const ZNumberInspector = ({znumber}) => { 
    let [min, setMin] = useState(znumber.min);
    let [max, setMax] = useState(znumber.max);
    let [round, setRound] = useState(znumber.round);

    useEffect(() => {
        setMin(znumber.min);
        setMax(znumber.max);
        setRound(znumber.round);
    }, [znumber, setMin, setRound, setMax]);

    const onChange = useCallback((e, setter, field) => {
        let value = e.target.value;
        let text = value;
        if (value === '.' || value === '-.') {
            value = 0;
        } else if (value === '-') {
            value = 0;
        } else if (value === '') {
            value = 0;
        } else if (!isNumber(value)) {
            return;
        }
        setter(text);
        znumber[field] = parseFloat(value);
    }, [setMax, setMin, znumber]);

    useEffect(() => {
        setRound(znumber.round);
    }, [setRound, znumber.round]);

    const handleRoundChange = useCallback(() => {
        znumber.round = !round;
        setRound(!round);
    }, [round, setRound, znumber]);

    return (
        <div className="z-number-inspector">
            <div className={"min-max"}>Min
              <input className="min-max-input"
                     value={min}
                     onChange={(e) => onChange(e, setMin, "min")} type="text"/>
            </div>
            <div className={"min-max"}>Max
              <input className="min-max-input"
                     value={max}
                     onChange={(e) => onChange(e, setMax, "max")} type="text"/>
            </div>
          <Checkbox label="Round" value={round} onChange={handleRoundChange}/>
        </div>
    );
}

const Checkbox = ({ label, value, onChange }) => {
  return (
    <label>
      <input type="checkbox" checked={value} onChange={onChange} />
      {label}
    </label>
  );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZNumberInspector);
