import React, { useEffect, useCallback, useState} from 'react';
import {
    setShowHigherOrderSequencer,
    } from 'actions/index.js';
import {connect} from 'react-redux';

const HigherOrderHeader = ({setShowHigherOrderSequencer, show}) => { 
    return (
        <div
          className="higher-order-component">
          <div className="higher-order-header">
            <div className="name">
              H=O Sequencer
            </div>
            <div className="collapse-container">
              <div onClick={() => setShowHigherOrderSequencer(!show)} className={show ? "arrow-down" : "arrow-left"}>
              </div>
            </div>
          </div>
        </div>
    );
}

const mapStateToProps = (state, props) => {
  return {
      show: state.higherOrder.show
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setShowHigherOrderSequencer: (x) => dispatch(setShowHigherOrderSequencer(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HigherOrderHeader);
