import React, { useRef, useEffect, useCallback, useState} from 'react';
import {useMouse} from './useMouse.js';
import {connect} from 'react-redux';
import {useContextMenu} from "components/dropdowns/useContextMenu.js";
import ContextMenu from "components/dropdowns/ContextMenu.js";
import MenuItem from "components/dropdowns/MenuItem.js";

let SIZE = 32;
const HigherOrderStepComponent = ({
    setDragging,
    dragging,
    isDragging,
    setSelected,
    orientation,
    stepData,
    size,
    higherOrderSequencer,
    isSelected,
    setSelection,
    x,
    y,
    ... props
}) => { 
    let ref = useRef(undefined);
    let acted = useRef(false);
    let [color, setColor] = useState(stepData.module && stepData.module.color);
    let className="higher-order-step";
    if (isDragging) {
        className += " dragging";
    }
    let {onContextMenu, dropdownPos, setDropdownPos} = useContextMenu({ref});
    let style = {};
    useEffect(() => {
        setColor(props.color);
    }, [props.color, setColor]);
    let _size = SIZE*size;
    className += " on";
    className += " orientation-" + orientation;

    const {onMouseDown, onMouseUp} = useMouse({
        setSelection,
        acted,
        setSelected,
        higherOrderSequencer,
        dragging,
        setDragging});

    let points=[x*SIZE,y*SIZE];
    if (orientation === 0) {
        points = [... points, x*SIZE, (y+size)*SIZE, (x+size)*SIZE, (y+size)*SIZE];
    } else {
        points = [... points, (x+size)*SIZE, (y)*SIZE, (x+size)*SIZE, (y+size)*SIZE];
    }

    const chooseModule = useCallback((module) => {
        if (!stepData) {
            return;
        }
        stepData.module = module;
        if (module) {
            setColor(module.color);
        }
    }, [x,y, setColor, stepData]);

    if (dropdownPos) {
        className += " context";
    }
    if (isSelected) {
        className += " selected";
    }
    let {subSequences} = higherOrderSequencer.sequencerEngine;
    return (
        <polygon
          points={points}
          ref={ref}
          onContextMenu={(e) => {
              if (stepData) {
                  onContextMenu(e);
                  setDragging(undefined);
              }
          }}
          onMouseDown={(e) => {
              e.stopPropagation();
              onMouseDown(x,y,e);
          }}
          onMouseUp={(e) => {
              e.stopPropagation();
              if (!dropdownPos) {
                  onMouseUp(x,y);
              }
          }}
          style={style} className={className}>
        {/*
          <ContextMenu dropdownPos={orientation === 0 ?
                                    dropdownPos && {x: (-_size) + 'vw', y: (-_size) + 'vw'} :
                                    dropdownPos} setDropdownPos={setDropdownPos}>
            <MenuItem
              onMouseUp={(e) => e.stopPropagation()}
              onMouseDown={(e) => {
                  e.stopPropagation();
                  chooseModule(null);
                  setDropdownPos(undefined);
              }}>
              <div className="module-name">All</div>
              <div  className="color-btn"></div>
            </MenuItem>
            {subSequences.map(
                module =>
                    <MenuItem
                      onMouseUp={(e) => e.stopPropagation()}
                      onMouseDown={(e) => {
                          e.stopPropagation();
                          chooseModule(module);
                          setDropdownPos(undefined);
                      }}>
                      <div className="module-name">{module.name} </div>
                      <div style={{backgroundColor: module.color}} className="color-btn"></div>
                    </MenuItem>)}
          </ContextMenu>
         */}
        </polygon>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HigherOrderStepComponent);
