import React, { useRef, useState, useEffect, useCallback} from 'react';;
export const useOrientation = ({ref, higherOrderSequencer, orientation}) => {
    let [dragging, setDragging] = useState(undefined);
    let [selectedEdge, setSelectedEdge] = useState(undefined);

    useEffect(() => {
        window.addEventListener("keydown", onKeyDown);
        return () => window.removeEventListener("keydown", onKeyDown);
    }, [selectedEdge, setSelectedEdge]);

    const onKeyDown = useCallback((e) => {
        if (!selectedEdge) {
            return;
        }
        if (e.key === 'Backspace') {
            let {from, to} = selectedEdge;
            from = higherOrderSequencer.getModule(from);
            if (to === -1) {
                higherOrderSequencer.disconnectFromFreeform(from);
            } else {
                to = higherOrderSequencer.getModule(to);
                higherOrderSequencer.disconnectModules(orientation, from, to);
            }
            setSelectedEdge(undefined);
        }
    }, [selectedEdge, setSelectedEdge]);

    const getMouseXY = (e) => {
        let rect = ref.current.getBoundingClientRect();
        let x = e.clientX - rect.left;
        let y = e.clientY - rect.top;
        return {x,y};
    };

    const onMouseUp = useCallback((mod) => {
        if (selectedEdge) {
            setSelectedEdge(undefined);
        }
        if (!dragging) {
            return;
        }

        higherOrderSequencer.connectModules(orientation, dragging.module, mod);
    }, [dragging, selectedEdge, setSelectedEdge]);

    const onMouseMove = useCallback((e) => {
        if (!dragging) {
            return;
        }
        let {x,y} = getMouseXY(e);
        setDragging({
            ... dragging,
            x2: x,
            y2: y
        });
    }, [dragging, setDragging]);

    const onMouseDown = useCallback((e, module) => {
        let {x,y} = getMouseXY(e);
        setDragging({
            module,
            x1: x,
            y1: y,
            x2: x,
            y2: y
        });
    }, [setDragging]);

    return {onMouseMove, onMouseUp, onMouseDown, setDragging, dragging, selectedEdge, setSelectedEdge};
};
