import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

const Choice = ({sequencer, orientation, type, name}) => { 
    let [chosen, setChosen] = useState(sequencer.algorithmTypes[orientation].includes(type));

    const toggle = useCallback(() => {
        if (chosen) {
            sequencer.algorithmTypes[orientation] =
                sequencer.algorithmTypes[orientation].filter(
                    n => n !== type);
        } else {
            sequencer.algorithmTypes[orientation] =
                [... sequencer.algorithmTypes[orientation],
                 type];
        }
        setChosen(!chosen);
    }, [sequencer, chosen, setChosen]);

    let className = "algorithm-choice";
    if (chosen) {
        className += " chosen";
    }
    return (<div onClick={toggle} className={className}>{name}</div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Choice);
