import React, { useRef, useEffect, useCallback, useState} from 'react';
import {setHigherOrderStepsSelected} from 'actions/index.js';
import {useOrientation} from './useOrientation.js';
import {connect} from 'react-redux';

let SIZE = 10;
let MARGIN= 17;

const TO_X = 70;
const FROM_X = SIZE;

const OrientationRouting = ({orientation, higherOrderSequencer, setHigherOrderStepsSelected}) => { 
    let ref = useRef(undefined);

    let {selectedEdge, setSelectedEdge, onMouseMove, onMouseUp, onMouseDown, setDragging, dragging} =
        useOrientation({ref, orientation, higherOrderSequencer});


    useEffect(() => {
        if (selectedEdge) {
            setHigherOrderStepsSelected(undefined);
        }
    }, [selectedEdge]);
    let routing = higherOrderSequencer.routings[orientation];
    let modules = higherOrderSequencer.modules;
    
    let getXY = (col=0, i) => {
        let x = col === 0 ? SIZE : TO_X;
        let y = SIZE+(SIZE+MARGIN)*i;
        return {x,y};
    };

    const getModNumber = (mod) => {
        let modules = higherOrderSequencer.modules;
        for (let i=0; i < modules.length; i++ ){
            if (modules[i].id === mod) {
                return i;
            }
        }
        return -1;
    };

    const getRectXY = useCallback(() => {
        let rectXY = getXY(0, higherOrderSequencer.modules.length);
        let rectX = 25;
        let rectY = rectXY.y;
        return {
            x: rectX,
            y: rectY,
        };
    }, []);

    const onFreeformUp = useCallback(() => {
        if (!dragging) {
            return;
        }
        higherOrderSequencer.connectToFreeform(dragging.module);
    }, [dragging]);

    const getPath = (x1, y1, x2, y2) => {
        return "M " +x1 + ', ' + y1 + ' C ' +
            (x1+x2)/2 +', ' + y1 + ',' + 
            (x1+x2)/2 +', ' + y2 + ','+
            (x2-10) +', ' + y2;
    };

    let routeKeys = Object.keys(routing);
    let rectXY = getRectXY();
    let rectX = rectXY.x;
    let rectY = rectXY.y;
    return (
        <svg
          height={rectY+15}
          width={100}
          ref={ref}
          onMouseUp={() => setDragging(undefined)}
          onMouseLeave={() => setDragging(undefined)}
          onMouseMove={onMouseMove}
          className="orientation-routing">
          <marker id="o-arrowhead" markerWidth="10" markerHeight="8" 
	          refX="4" refY="2" orient="auto">
            <polygon points="0 0, 5 2, 0 4" fill="white"/>
          </marker>
          {dragging && <line
                         stroke="white" x1={dragging.x1} x2={dragging.x2} y1={dragging.y1} y2={dragging.y2}/>}
          {higherOrderSequencer.freeformModules.map(
              module => {
                  let fromPos = getXY(0, getModNumber(module.id));
                  let toPos = getRectXY();
                  let x1 = fromPos.x;
                  let y1 = fromPos.y;
                  let x2 = toPos.x + 15;
                  let y2 = toPos.y;
                  let d = `M ${x1},${y1} C ${x1+30}, ${y1} ${x1+15}, ${y1+5} ${x2}, ${y2}`;
                  let className= "module-edge";
                  if (selectedEdge && selectedEdge.to === -1 &&
                      selectedEdge.from === module.id) {
                      className += " selected";
                  }
                  return (<path
                            onMouseUp={(e) => {
                                e.stopPropagation();
                                setSelectedEdge({
                                    from: module.id,
                                    to: -1
                                });
                            }}
                            className={className}
                            markerEnd="url(#o-arrowhead)"
                            stroke="white"
                            d={d}
                          />);
              })}
          {routeKeys.map(
              from =>
                  routing[from].map(
                      to => {
                          let fromPos = getXY(0, getModNumber(from));
                          let toPos = getXY(1, getModNumber(to.id));
                          let d = getPath(fromPos.x, fromPos.y, toPos.x, toPos.y);
                          let className = "module-edge";
                          if (selectedEdge) {
                              if (selectedEdge.from === from &&
                                  selectedEdge.to === to.id) {
                                  className += " selected";
                              }
                          }

                          return (
                              <path
                                onMouseUp={(e) => {
                                    e.stopPropagation();
                                    setSelectedEdge({
                                        from: from,
                                        to: to.id
                                    });
                                }}
                                d={d}
                                markerEnd="url(#o-arrowhead)"
                                stroke="white"
                                className={className}
                              />);
                      }))}

          {modules.map(
              (mod, i) =>
                  <g>
                    <circle
                      onMouseDown={(e) => onMouseDown(e, mod)}
                      fill="white" cx={getXY(0, i).x} cy={getXY(0, i).y} r={SIZE}/>
                    <text x={2+SIZE*0.5} y={0.5*SIZE+getXY(0,i).y} fill="black">
                      {mod.name}
                    </text>
                  </g>)}
          {modules.map(
              (mod, i) =>{
                let {x,y} = getXY(1, i);
                  return (
                      <g>
                        <circle
                          onMouseUp={() => onMouseUp(mod)}
                          fill="white" cx={x} cy={y} r={SIZE}/>
                        <text x={2+x-SIZE*0.5} y={0.5*SIZE+y} fill="black">
                          {mod.name}
                        </text>
                      </g>);
              })}
          <rect
            onMouseUp={onFreeformUp}
            fill="white" x={rectX} y={rectY} width={30} height={15}/>
          <text fill="black" x={6+rectX} y={rectY+11}>Free</text>
        </svg>
    );
};

const mapStateToProps = (state, props) => {
  return {
      orientationRoutings: state.higherOrder.orientationRoutings,
      modulesLen: props.higherOrderSequencer.modules.length
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setHigherOrderStepsSelected: x=>dispatch(setHigherOrderStepsSelected(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(OrientationRouting);
