import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import HigherOrderStepComponent from './HigherOrderStepComponent.js';

const HigherColumn = ({
    x,
    higherOrderSequencer,
    dragging,
    setDragging,
    selected,
    setSelected,
    selection,
    setSelection
}) => { 
    const justPressed = useRef(false);
    const onMouseUp = useCallback((y, e) => {
        if (selection) {
            setSelected(higherOrderSequencer.getSelection(selection));
            return setSelection(undefined);
        }

        if (dragging && dragging.x===x && dragging.y==y) {
            if (!justPressed.current && !e.metaKey) {
                higherOrderSequencer.toggle(x,y);
            }
        }
        setDragging(undefined);
        justPressed.current = false;
    }, [x, setSelected, setDragging, dragging, selection, setSelection]);

    const onMouseOver  = useCallback((y) => {
        if (selection) {
            setSelection({... selection, x2: x, y2: y});
            return;
        }

        if (!dragging) return;

        let xDist = x-dragging.x;
        let yDist = y-dragging.y;
        let dist = Math.max(x - dragging.x, y - dragging.y);
        if (dist >= 0) {
            higherOrderSequencer.setStepSize(
                dragging.x,
                dragging.y,
                dist+1);
        } 
    }, [dragging, setDragging, selection, setSelection]);

    const onMouseDown = useCallback((y, e) => {
        if (e.metaKey) {
            setSelection({x1: x, y1: y, x2: x, y2: y});
            return;
        }

        if (!higherOrderSequencer.matrix[x][y]) {
            justPressed.current=true;
            higherOrderSequencer.toggle(x,y);
        }
        setDragging({x,y});
        setSelected([higherOrderSequencer.matrix[x][y]]);
    }, [setSelection, selection, setDragging]);

    const getStepClass = useCallback((stepData, y) => {
        let className = "higher-step";
        if (y%2 === 0) {
            className += " even";
        }
        if (selected && selected.includes(stepData)) {
            className += " selected";
        }
        if (selection) {
            let {x1, y1, x2, y2} = selection;
            if (x1 <= x && x <= x2 &&
                y1 <= y && y <= y2) {
                className += " selection";
            }
        }
        return className;
    }, [selected, selection]);

    let className = "higher-col";
    if (x%2===0) {
        className += " even";
    } 
    if (dragging) {
        className += " dragging";
    }
   return (
        <div className={x%2==0 ? "higher-col even" :"higher-col"}>
          {higherOrderSequencer.matrix[x].map(
              (obj, y) =>
                  <div
                    onMouseLeave={() => justPressed.current=false}
                    onMouseOver={() => onMouseOver(y)}
                    onMouseDown={(e) => onMouseDown(y,e)}
                    onMouseUp={(e) => onMouseUp(y,e)}
                    className={getStepClass(obj, y)}>
                 </div>)}
        </div>
    );
};

export default HigherColumn;
