import React, { useEffect, useCallback, useState} from 'react';
import SubMenu from "components/dropdowns/SubMenu.js";
import {useMintToHic} from 'components/hooks/useMintToHic.js';
import {useMintToZora} from 'components/hooks/useMintToZora.js';
import ContextMenu from "components/dropdowns/ContextMenu.js";
import MenuItem from "components/dropdowns/MenuItem.js";
import {
    setWindowPosition,
    setViewableZObjects,
    setFullScreenZObject} from 'actions/index.js';

import {connect} from 'react-redux';

const ZObjectContextMenu = ({
    setViewableZObjects,
    dropdownPos,
    zobject,
    setDropdownPos,
    setFullScreenZObject,
    setWindowPosition,
}) => { 
    const {mintToHic} = useMintToHic({zobject, setDropdownPos});
    const {mintToZora} = useMintToZora({zobject, setDropdownPos});

    const duplicate = useCallback(() => {
        zobject.duplicate();
        setDropdownPos(undefined);
    }, [setDropdownPos]);

    const fullScreen = useCallback(() => {
        setFullScreenZObject(zobject);
        setDropdownPos(undefined);
    }, [setDropdownPos]);

    const openAsWindow = useCallback(() => {
        let {viewableZObjects} = zobject.state.zobject;
        setWindowPosition({x: 100, y: 100});
        setViewableZObjects([... viewableZObjects, zobject]);
    }, [zobject, setViewableZObjects]);

    const deleteZObject = useCallback(() => {
        zobject.delete();
    }, [zobject]);

    return (
        <ContextMenu dropdownPos={dropdownPos} setDropdownPos={setDropdownPos}>
          <MenuItem onMouseDown={fullScreen}>
            Fullscreen 
          </MenuItem>
          <MenuItem onMouseDown={openAsWindow}>
            Open as Window
          </MenuItem>
          <MenuItem onMouseDown={duplicate}>
            Duplicate <div className="key-shortcut">D</div>
          </MenuItem>
          <div className="divider"/>
          <SubMenu name="Mint On...">
            <MenuItem onMouseDown={mintToZora}>
              Zora
            </MenuItem>
            <MenuItem onMouseDown={mintToHic}>
              Hicetnunc/Objkt
            </MenuItem>
          </SubMenu>
          <MenuItem onMouseDown={deleteZObject}>
            Delete
          </MenuItem>
        </ContextMenu>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
      setFullScreenZObject: x => dispatch(setFullScreenZObject(x)),
      setWindowPosition: x => dispatch(setWindowPosition(props.zobject.window.id, x)),
      setViewableZObjects: x => dispatch(setViewableZObjects(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZObjectContextMenu);
