import React, { useEffect, useCallback, useState} from 'react';
import PlaneSidebar from "components/effects/plane/PlaneSidebar.js";
import {connect} from 'react-redux';
import {
    setPlanarRegionMode,
    setPlanarMacroToShow,
    setFilteredPlanarParameters,
    setSelectedPlanarParameters,
    setEffectParameterValues} from 'actions/index.js';

const PlaneTools = ({
    effect,
    setIsRegionMode,
    setMacroToShow,
    setSelected,
    setFilteredParameters,
    selected=[],
    filteredParameters,
    macroToShow,
    isRegionMode
}) => { 
    return (
        <div className="plane-parameters">
          <div className="plane-inner">
            <PlaneSidebar
              setSelectedColor={() => 0}
              macroToShow={macroToShow}
              setMacroToShow={setMacroToShow}
              isBoundsMode={isRegionMode}
              setSelected={setSelected}
              expanded={false}
              filteredParameters={filteredParameters}
              setFilteredParameters={setFilteredParameters}
              selected={selected}
              effect={effect}
              setSelected={setSelected}
            />
          </div>
        </div>
    );
};

const mapStateToProps = (state, props) => {
    return {
        selected: state.plane.selectedParameters[props.effect.id],
        filteredParameters: state.plane.filteredParameters[props.effect.id],
        macroToShow: state.plane.macroToShow[props.effect.id],
        isRegionMode: state.plane.regionMode[props.effect.id]
  };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
        setIsRegionMode: x => dispatch(setPlanarRegionMode(props.effect.id, x)),
        setMacroToShow: x => dispatch(setPlanarMacroToShow(props.effect.id, x)),
        setSelected: x => dispatch(setSelectedPlanarParameters(props.effect.id, x)),
        setFilteredParameters: x => dispatch(setFilteredPlanarParameters(props.effect.id, x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PlaneTools);
