import React, {useCallback} from 'react';
export const useMouse = ({setSelection, acted, setSelected, higherOrderSequencer, dragging, setDragging}) => {
    const onMouseUp = useCallback((x, y) => {
        if (acted.current && dragging && dragging.x===x && dragging.y==y) {
            higherOrderSequencer.toggle(x,y);
        }
        setDragging(undefined);
    }, [setDragging, dragging]);

    const onMouseDown = useCallback((x, y, e) => {
        if (e.metaKey) {
            setSelection({x1: x, y1: y, x2: x, y2: y});
            return;
        }
        // higherOrderSequencer.toggle(x,y);
        setDragging({x,y});
        setSelected([higherOrderSequencer.matrix[x][y]]);
        acted.current = true;
    }, [setDragging, setSelection]);

    return {onMouseDown, onMouseUp};
};
