import React, {useRef, useEffect, useCallback, useState} from 'react';
import {getAllNodesInSub} from 'utils/findNode.js';
import ObjectOption from './ObjectOption.js';
import {connect} from 'react-redux';

const NestedZObject = ({
    setObjectDragging,
    setSelectedZObject, selectedZObject, zobject, searchTerm
}) => { 
    let ref=useRef();
    let [dropActive, setDropActive] = useState(false);
    let [show, setShow] = useState(false);
    let className="zobject-option";
    if (zobject === selectedZObject) {
        className += " selected";
    }

    let isNested = zobject.effect && zobject.effect.zObjects;
    if (isNested) {
        className += " nested";
    }

    useEffect(() => {
        setShow(searchTerm !== "");
    }, [searchTerm, setShow]);

    if (searchTerm !== "") {
        searchTerm = searchTerm.toLowerCase();
        if (zobject.effect && zobject.effect.zObjects) {
            if (zobject._text.toLowerCase().includes(searchTerm)) {
                searchTerm = "";
            } else {
                let objects= getAllNodesInSub(zobject.effect);
                if (!objects.some(
                    o => typeof o._text === "string" &&
                        o._text.toLowerCase().includes(searchTerm))) {
                    return '';
                }
            }
        } else {
            if (typeof zobject._text !== "string" || !zobject._text.toLowerCase().includes(searchTerm)) {
                return '';
            }
        }
    }

    const onDrop = useCallback(() => {
        ref.current.setAttribute("drop-active", 0);
        if (zobject.effect && zobject.effect.zObjects) {
            let objectDragging = zobject.state.zobject.objectDragging;
            // we want to move this object from its old to this new one
            zobject.effect.moveObjectInto(objectDragging);
        }
    }, [zobject]);

    let _className ="zobject";
    if (dropActive) {
        _className += " drop-active";
    }
    return (
        <div
          ref={ref}
          onDragEnter={e => {
              e.preventDefault();
              if (zobject.effect && zobject.effect.zObjects) {
                  ref.current.setAttribute("drop-active", 1);
              }
          }}
          onDragOver={e => () => {
              e.preventDefault();
          }}
          onDrop={onDrop}
          onDragLeave={e => {
              console.log("on drag leave");
              ref.current.setAttribute("drop-active", 0);
          }}
          className={_className}>
          <ObjectOption
            setObjectDragging={setObjectDragging}
            className={className}
            setShow={setShow}
            show={show}
            zobject={zobject}
            setSelectedZObject={setSelectedZObject}
          />
          {show && zobject.effect && zobject.effect.zObjects &&
           <div
             onDragLeave={e => e.preventDefault()}
             className="nested-inner">
             {zobject.effect.zObjects.map(
                 obj => <NestedZObject
                          setObjectDragging={setObjectDragging}
                          key={obj.id}
                          zobject={obj}
                          searchTerm={searchTerm}
                          setSelectedZObject={setSelectedZObject}
                          selectedZObject={selectedZObject}/>)}
           </div>}
        </div>);
};

export default NestedZObject;
