import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

import './zobject.scss';
const ZObjectInformation = ({zobject, coords}) => { 
    let src = zobject.name === 'comment' ? '/zcomment-light.svg': '/zobject-light.svg';
    if (zobject.plugin) {
        src = "/plugin-icon.svg";
    }
    return (<div className="zobject-information object-information">
              <img className="zobject-information-icon" src={src}/>
              <div className="object-name">
                {zobject.plugin ? zobject.plugin.name : zobject.name}
                {coords && <div className="coords">
                             {coords.x}, {coords.y}
                           </div>}
              </div>
            </div>
           );
}

const mapStateToProps = (state, props) => {
  let presentationMode = state.spatial.presentationMode;
  return {
      name: props.zobject.name,
      coords: presentationMode ?
          state.windows.presentationWindowPositions[props.zobject.id] || state.windows.windowPositions[props.zobject.id] :
          state.windows.windowPositions[props.zobject.id],
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZObjectInformation);
