import React, { useEffect, useCallback, useState} from 'react';
import PluginsList from './PluginsList.js';
import {
    setShowPlugins,
} from 'actions/index.js';
import {connect} from 'react-redux';
import './plugins.scss';

const PluginsViewer = ({sequencerEngine, setShowPlugins}) => { 
    return (
        <div className="plugins-viewer">
          <div className="plugins-header">
            <div
              onClick={() => setShowPlugins(false)}
              className="back-arrow-container pushable">
              <div className="arrow-left"/>
            </div>
            <img className="plugins-icon" src="/plugin-icon.svg"/>
            <div className="header-text">Plugins</div>
          </div>
          <PluginsList sequencerEngine={sequencerEngine}/>
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setShowPlugins: m => dispatch(setShowPlugins(m))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PluginsViewer);
