import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import TrackEffects from "components/mix/mix2/TrackEffects.js";
import MidiEffects from "components/mix/mix2/MidiEffects.js";

const TrackQuickEffects = ({track}) => { 
    return track && track.effects && (
        <TrackEffects
          hasMidi={true}
          needsLabel={true}
          showEffect={true}
          track={track}/>);
    
    ;
}

const mapStateToProps = (state, props) => {
  return {
      effects: props.track && props.track.effects ? props.track.effects.length : 0
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TrackQuickEffects);
