import React, { useEffect, useCallback, useState} from 'react';
import {freezePatterns} from 'components/v2/trackRightClickActions.js';
import TrackFrozenComponent from "components/v2/TrackFrozenComponent.js";
import Colors from 'components/Colors.js';
import CheckboxParameter from "components/effects/CheckboxParameter.js";
import {connect} from 'react-redux';

const colors = new Colors();

const TrackFreezes = ({isFrozen, track}) => { 
    return (
        <div className="sidebar-section">
          <div className="label">
            Freeze
          </div>
          {isFrozen ?
           <TrackFrozenComponent
             track={track}
             trackColor={colors.getColorForTrack(track)}
             isTrackFrozen={true}/> : 
           <div onClick={() => freezePatterns(track)} className="freeze-button">
             Freeze Track
           </div>}
        </div>
    );
}

const mapStateToProps = (state, props) => {
  return {
      isFrozen: props.track.isFrozen
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TrackFreezes);
