import React, { useEffect, useCallback, useState} from 'react';
import {setWindowPosition, setViewableSubPatches} from 'actions/index.js';
import {connect} from 'react-redux';
import {getParents} from './getParents.js';

const SubPatchBreadcrumb = ({subpatch, setWindowPosition, setViewableSubPatches}) => { 
    let parents = getParents(subpatch);

    const goTo = useCallback((_subpatch) => {
        let {state} = subpatch.zObject.sequencerEngine;
        let subpatches = state.zobject.viewableSubPatches.filter(
            x => !parents.includes(x) && x !== subpatch);
        let _parents = [... parents];
        _parents.reverse();

        let toAdd = [];

        for (let s of _parents) {
            toAdd.push(s);
            if (s === _subpatch) {
                break;
            }
        }

        _subpatch.width = subpatch.width;
        _subpatch.height = subpatch.height;
        let position = state.windows.windowPositions[subpatch.id];
        setWindowPosition(_subpatch.id, position);
        setViewableSubPatches([... subpatches, ... toAdd]);
    }, [parents, setViewableSubPatches, subpatch]);

    return (
        <div className="subpatch-breadcrumb">
          {parents.map(
              (x) =>
                  <div className="parent">
                    <div className="arrow-right"/>
                    <div onClick={() => goTo(x)} className="parent-name">{x.zObject.args[0]}</div>
                  </div>
          )}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setViewableSubPatches: x => dispatch(setViewableSubPatches(x)),
      setWindowPosition: (id, x) => dispatch(setWindowPosition(id, x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubPatchBreadcrumb);
