import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

let size = 32;
const Playhead = ({playing, trackStep}) => { 
    let style = {
        left: (trackStep % 32) * 16
    };
    if (!playing) {
        return '';
    }
    return (
        <div style={style} className="higher-playhead">
        </div>
    );
}

const mapStateToProps = (state, props) => {
  return {
      trackStep: props.sequencerEngine.trackStep,
      playing: props.sequencerEngine.playing
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Playhead);
