import React, { useEffect, useCallback, useState} from 'react';
import ConditionedCheckmark from "components/ConditionedCheckmark.js";
import {getAllNodesInSub} from "utils/findNode.js";
import {setSelectedZObject} from 'actions/index.js';
import {connect} from 'react-redux';

const ObjectConnector = ({zobject, connects, setSelectedZObject}) => { 
    let [value, setValue] = useState(undefined);

    const connectObject = useCallback((object, inlet="Input", outlet) => {
        // first see if it exists
        let found = zobject.subSequence.zObjects.find(x => x.name === object);
        if (!found) {
            if (object === "pad") {
                if (zobject.subSequence.pads.length === 0) {
                    found = zobject.subSequence.createPad();
                } else {
                    found = zobject.subSequence.pads[0];
                }
            } else {
                found = zobject.subSequence.createZObject(
                    undefined,
                    undefined,
                    object);
            }
        }

        let fromObject = zobject;
        if (outlet && outlet in zobject.outlets) {
        } else if (outlet && zobject.effect && outlet in zobject.effect.outlets) {
            fromObject = zobject.effect;
        } else {
            fromObject = zobject.effect && Object.keys(zobject.effect.outlets)[0] ?
                zobject.effect :
                zobject;
        }

        let obj = found;
        if (found.effect && inlet in found.effect.inlets) {
            found = found.effect;
        }

        found.connectInlet(
            inlet,
            fromObject,
            outlet ||
                Object.keys(zobject.outlets)[0] ||
                (zobject.effect &&
                 Object.keys(zobject.effect.outlets)[0]));

        setValue(object);
        setSelectedZObject(obj);
    }, [zobject, connects, setValue]);

    if (!Array.isArray(connects)) {
        connects = [connects];
    }

    const isConnected = useCallback((object, inlet, outlet) => {
        let outlets = {
            ... zobject.outlets,
            ... (zobject.effect ? zobject.effect.outlets : {})
        };
        let _outlet = outlet ? outlets[outlet] : outlets[Object.keys(outlets)[0]];
        if (_outlet) {
            return _outlet.connections.some(
                x => x.destNode.name === object || (object === "pad" && x.destNode.isPad));
        }
    }, [zobject]);
    
    return (
        <div className="object-connector">
          <div className="connect-help">
            Commonly connects with
          </div>
        {connects.map(
            x =>
                <div onClick={() => connectObject(x.object, x.inlet, x.outlet)}
                     className={isConnected(x.object, x.inlet, x.outlet) ? "connect-option connected " : "connect-option"}>
                  <img className="connects-with-icon" src="/connects-with-icon.svg"/>
                  <div className="object-type">
                    {x.object}
                  </div>
                  <div className="inlet-type">
                    {x.inlet}
                  </div>
                  <ConditionedCheckmark
                    className="guilds-checkmark"
                    condition={isConnected(x.object, x.inlet, x.outlet)}/>
                </div>)}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSelectedZObject: x => dispatch(setSelectedZObject(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ObjectConnector);
