import React, { useEffect, useCallback, useState} from 'react';
import EffectParameterSlider from "components/effects/EffectParameterSlider.js";
import ParameterSearcher from './ParameterSearcher.js';
import {
    setAccumulatorParameterSelected,
    setIsMacroMapping} from 'actions/index.js';
import {connect} from 'react-redux';
import ParametersMapper from "components/effects/ParametersMapper.js";

const HigherOrderParameters = ({higherOrderSequencer}) => { 
    let [parameters, setParameters] = useState([]);
    return (
        <div className="higher-order-parameters">
          <div>
            <ParametersMapper parametersList={parameters} effect={higherOrderSequencer}/>
            <EffectParameterSlider
              name="Depth"
              sequencerEngine={higherOrderSequencer.sequencerEngine}
              min={0}
              max={1}
              step={0.001}
              parameter={higherOrderSequencer.automationDepthParameter}/>
          </div>
          <ParameterSearcher parametersList={parameters} setParameters={setParameters} higherOrderSequencer={higherOrderSequencer}/>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setIsMacroMapping: (isMapping) => dispatch(
            setIsMacroMapping(isMapping)),
      setAccumulatorParameterSelected: (param) => dispatch(
          setAccumulatorParameterSelected(param))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HigherOrderParameters);
