import React, { useEffect, useCallback, useState} from 'react';
import ipfs from "decentralized/Ipfs.js";
import {connect} from 'react-redux';

const NAMES = ['A', 'B', 'C', 'D', 'E', 'F', 'G'];

const ModuleOption = ({project, sequencerEngine}) => { 
    let [data, setData]= useState(undefined);
    let [modules, setModules] = useState([]);
    let [module, setModule] = useState(undefined);
    useEffect(() => {
        ipfs.getJson(project.newSequence).then(setData);
    }, [setData]);

    const onClickModule = useCallback((i) => {
        sequencerEngine.loadSequence(project.newSequence, i);
    }, [project]);

    useEffect(() => {
        if (data) {
            setModules(data.subSequences);
        }
    }, [data, setModules]);
    return (
        <div
          className="module-option">
          <div className="basic">
            <div className="project-name">{project.title}</div>
            <div className="module-types">
              {modules.map((x, i) => <div
                                       onClick={() => onClickModule(i)}
                                       className="module-name">{NAMES[i]}</div>)}
            </div>
          </div>
          {module !== undefined &&
           <div className="expansion">
           </div>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ModuleOption);
