import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

const ModuleRouter = ({inputModules, outputModules, higherOrderSequencer}) => { 
    let modules = higherOrderSequencer.sequencerEngine.subSequences;
    return (
        <div className="module-router">
        {modules.map(
            mod =>
                <div className="module-routes">
                  <div className="module-indicator">
                    {mod.name}
                  </div>
                  <img
                    onClick={() => higherOrderSequencer.toggleInput(mod)}
                    src={!inputModules.includes(mod) ?
                            "/black-arrow.svg" :
                            "/gray-arrow.svg"}/>
                </div>
        )}
        </div>);
}

const mapStateToProps = (state, props) => {
    let modules = props.higherOrderSequencer.sequencerEngine.subSequences;
    return {
        num: modules.length,
        inputModules: state.higherOrder.inputModules,
        outputModules: state.higherOrder.outputModules,
    };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ModuleRouter);
