import React, { useRef, useEffect, useCallback, useState} from 'react';
import SubPatchSidebar from './SubPatchSidebar.js';
import SubPatchObjectSidebar from './SubPatchObjectSidebar.js';
import {useArrangeWindows} from "components/windows/useArrangeWindows.js";
import {segmentAllCables} from 'messaging/cables/segmentCables.js';
import {useSubPatchScroll} from './useSubPatchScroll.js';
import SubPatchBreadcrumb from './SubPatchBreadcrumb.js';
import {getParents} from './getParents.js';
import SubPatchMenu from 'components/subpatch/SubPatchMenu.js';
import ZObjectToolbar from "components/pulled/ZObjectToolbar.js";
import {useWindows} from "components/windows/useWindows.js";
import SubPatchColors from './SubPatchColors.js';
import {useContextMenu} from "components/dropdowns/useContextMenu.js";
import SubPatchContent from './SubPatchContent.js';
import {setModuleSize} from 'actions/index.js';
import ContextMenu from "components/dropdowns/ContextMenu.js";
import MenuItem from "components/dropdowns/MenuItem.js";
import {connect} from 'react-redux';
import {NW, NE, SW, SE} from 'components/windows/resize-types.js';

import {useWindowManager} from "components/windows/useWindowManager.js"
import "../../subpatch.scss";
import {setWindowPosition, setViewableSubPatches} from 'actions/index.js';
import {useDoubleClick} from "components/hooks/useDoubleClick.js";


const SubPatch = ({
    sizeRef,
    ordering,
    setOrdering,
    elements,
    setSize,
    subpatch,
    subPatches,
    setViewableSubPatches,
    setWindowPosition,
    containerRef,
    windowPosition,
    backgroundColor,
    presentationMode
}) => { 
    let ref = useRef(undefined);
    let contentRef = useRef(undefined);

    let {arrangeWindows} = useArrangeWindows({elements, subSequence: subpatch, presentationMode});
    const {onContextMenu, dropdownPos, setDropdownPos} = useContextMenu({ref: contentRef});
    const dropdownPosRef = useRef(dropdownPos);
    let [customMode, setCustomMode] = useState(subpatch.isCustom);
    let [editLayout, setEditLayout] = useState(false);

    const {windowScale, setWindowScale, onMouseDown, onMouseUp, style, startResizing} = useWindowManager({
        sequencerEngine: subpatch.sequencerEngine,
        basicContainerRef: containerRef,
        ref,
        ordering,
        setOrdering,
        element: subpatch,
        setSize,
        sizeRef,
        elements,
        windowPosition
    });

    let scrollRef = useRef();
    useSubPatchScroll({subpatch, ref: scrollRef, size: windowScale, setSize: setWindowScale});

    useEffect(() => {
        ref.current.addEventListener("mousewheel", onWheel);
        return () => {
            if (ref.current) {
                ref.current.removeEventListener("mousewheel", onWheel);
            }
        }
    }, []);

    const onWheel = (e) => {
        e.stopPropagation();
    };
    
    const onDoubleClickHeader = useCallback(() => {
        let width = window.innerWidth;
        let height = window.innerHeight;
        if (subpatch.width + 100 > window.innerWidth &&
            subpatch.height + 100 > window.innerHeight) {
            width = 600;
            height= 600;
        } 
        subpatch.width = width;
        subpatch.height = height;
        setWindowScale({
            x: width,
            y: height
        });
        setWindowPosition(subpatch.id, {x: 0, y: 0});
    }, [subpatch, setWindowScale]);

    const onHeaderClick = useDoubleClick(onDoubleClickHeader);

    let orderingRef = useRef(ordering);
    useEffect(() => {
        orderingRef.current = ordering;
    }, [ordering]);

    useEffect(() => {
        return () => {
            delete elements.current[subpatch.id];
            setOrdering(orderingRef.current.filter(x => x !== subpatch));
        };
    }, []);
    useEffect(() => {
        if (dropdownPos) {
            dropdownPosRef.current = dropdownPos;
        }
    }, [dropdownPos]);
 
    const hideSubPatch = useCallback((e) => {
        e.stopPropagation();
        let parents = getParents(subpatch);
        let _subPatches = subPatches.filter(x => x !== subpatch && !parents.includes(x));
        setViewableSubPatches(_subPatches);
    }, [subPatches]);

    const deencapsulate = useCallback(() => {
        subpatch.deencapsulate();
    }, [subpatch]);

    const createZMessage = useCallback(() => {
        let {x,y} = dropdownPosRef.current;
        subpatch.createZMessage(x,y);
    }, [subpatch]);

    const createInlet = useCallback(() => {
        let {x,y} = dropdownPosRef.current;
        subpatch.newInlet(undefined, undefined, x,y);
    }, [subpatch]);

    const createInstanceInlet = useCallback(() => {
        let {x,y} = dropdownPosRef.current;
        subpatch.newInstanceInlet(undefined, undefined, x,y);
    }, [subpatch]);

    const createOutlet = useCallback(() => {
        let {x,y} = dropdownPosRef.current;
        subpatch.newOutlet(undefined, undefined, x, y);
    }, [subpatch]);

    const includeInPresentation = useCallback(() => {
        let pulledElements = subpatch.sequencerEngine.state.windows.selectedElements;
        let ids = Object.keys(pulledElements);
        for (let id of ids) {
            let node = subpatch.sequencerEngine.findNodeById(id);
            if (node) {
                node.setAttribute("Include in Presentation", true);
            }
        }
    }, [subpatch]);

    let _style = {... style};
    if (windowScale && windowScale.x > 0 && windowScale.y  > 0) {
        _style.width = windowScale.x;
        _style.height = windowScale.y;
    }

    if (windowPosition) {
        _style.top = Math.min(window.innerHeight - 100, Math.max(0, windowPosition.y));
        _style.left = windowPosition.x;
    }
    let zIndex = _style.zIndex + 1000000000;
    _style.zIndex = zIndex;

    let className = "sub-patch no-select " ;
    if ((customMode && editLayout)) {
        className += " editing-layout-mode";
    }
    if (ordering.indexOf(subpatch) === ordering.length - 1) {
        className += " top-window";
    }

    const segment = useCallback(() => {
        segmentAllCables(subpatch.sequencerEngine, subpatch);
    }, [subpatch]);

    const encapsulate = useCallback(() => {
        subpatch.encapsulateObjects(dropdownPos.x, dropdownPos.y);
    }, [dropdownPos]);

    let _elements = subpatch.state.windows.selectedElements;
    let selected = Object.keys(_elements).filter(x =>
        _elements[x]);

    _style.backgroundColor = backgroundColor;
    return (
        <div
          onClick={e => {
              console.log("SUB PATCH CLCIKED");
          }}
          onMouseDown={onMouseDown}
          onMouseUp={onMouseUp}
          className={className} style={_style} ref={ref}>
          <div
            onMouseDown={(e) => startResizing(NW, e)}
            className="top-left-edge"/>
          <div
            onMouseDown={(e) => startResizing(NE, e)}
            className="top-right-edge"/>
          <div
            onMouseDown={(e) => startResizing(SW, e)}
            className="bottom-left-edge"/>
          <div
            onMouseDown={(e) => startResizing(SE, e)}
            className="bottom-right-edge"/>
          <div onClick={onHeaderClick} className="sub-patch-header">
            {subpatch.zObject.args[0]}
            <SubPatchBreadcrumb subpatch={subpatch}/>
            <div
              onMouseDown={e => e.stopPropagation()}
              onClick={hideSubPatch}
              className="delete-btn">x</div>
          </div>
          <div
            ref={contentRef}
            onContextMenu={onContextMenu}
            onMouseDown={(e) => {
                e.stopPropagation();
                setDropdownPos(undefined);
            }}
            className="sub-patch-content">
            <ContextMenu className="midi-context" dropdownPos={dropdownPos ? {
                x: dropdownPos.x,
                y: dropdownPos.y} : undefined}>
              {selected.length > 0 &&
               <MenuItem
                 onMouseDown={encapsulate}>
                 Encapsulate Objects
               </MenuItem>}
              {selected.length > 0 && <MenuItem
                                        onMouseDown={includeInPresentation}>
                                        Include in Presentation
                                        <img src="/presentation-mode.svg"/>
                                      </MenuItem>}
              {selected.length > 0 &&
               <MenuItem
                 onMouseDown={arrangeWindows}>
                 Pack Objects
               </MenuItem>}
              <MenuItem
                onMouseDown={deencapsulate}>
                De-encapsulate entire subpatch
              </MenuItem>
              <MenuItem
                onMouseDown={createInlet}>
                New Inlet 
              </MenuItem>
              <MenuItem
                onMouseDown={createOutlet}>
                New Outlet 
              </MenuItem>
              <MenuItem
                onMouseDown={segment}>
                Segment All Cables 
              </MenuItem>
              <MenuItem
                onMouseDown={createInstanceInlet}>
                New Instance Inlet 
              </MenuItem>
            </ContextMenu>
            <ZObjectToolbar subpatch={subpatch} subSequence={subpatch}>
              <SubPatchMenu
                setEditLayout={setEditLayout}
                editLayout={editLayout}
                setCustomMode={setCustomMode}
                customMode={customMode}
                subpatch={subpatch}/>
            </ZObjectToolbar>
            <div className="content-and-sidebars">
              <SubPatchObjectSidebar
                sequencerEngine={subpatch.sequencerEngine}
                subpatch={subpatch}/>
              <SubPatchContent
                editLayout={editLayout}
                scrollRef={scrollRef}
                customMode={customMode}
                subpatch={subpatch}/>
              <SubPatchSidebar
                sequencerEngine={subpatch.sequencerEngine}
                subpatch={subpatch}/>
            </div>
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
      presentationMode: state.spatial.presentationMode,
      windowPosition: state.windows.windowPositions[props.subpatch.id],
      backgroundColor: state.sequencer.backgroundColor
  };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
      setWindowPosition: (id, pos) => dispatch(setWindowPosition(id, pos)),
      setViewableSubPatches: x => dispatch(setViewableSubPatches(x)),
      setSize: x => dispatch(
          setModuleSize(props.subpatch.id, x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubPatch);
