import React, { useEffect, useCallback, useState} from 'react';
import OperationDocs from './OperationDocs.js';
import SubPatchTools from './SubPatchTools.js';
import ZObjectActions from './ZObjectActions.js';
import AttributesInspector from './AttributesInspector.js';
import ObjectConnector from './ObjectConnector.js';
import {doc} from 'zobject/definitions/docs.js';
import {connect} from 'react-redux';

const ZObjectTools = ({zobject, name, inputText}) => { 
    if (zobject.pluginViewerFor) {
        zobject = zobject.pluginViewerFor;
    }
    let [value, setValue] = useState();
    const onClick = useCallback((op) => {
        zobject.text = op;
        setValue(op);
    }, [zobject, setValue]);

    const sendBang = useCallback(() => {
        zobject.bang();
    }, [zobject]);

    const onClickOperation = useCallback((operation, args) => {
        // create a message
        let zmessage = zobject.subSequence.createZMessage(
            undefined,
            undefined,
            undefined,
            undefined,
            undefined,
            operation + ' ' + args.map(x => 0).join(' ')
            );
        zobject.connectInlet("Input", zmessage, Object.keys(zmessage.outlets)[0]);
    }, [zobject]);

    if (!zobject.name || zobject.name === "") {
        let tags = doc(1);
        let sorted = Object.keys(tags).sort();
        return sorted.map(
            tag =>
                <div className="zobject-tag multi-object">
                  <div className="tag-name">{tag}</div>
                  <div className="tag-options">
                    {tags[tag].sort().map(
                        option =>
                            <div onClick={() => onClick(option)} className="tag-option">
                              <div className="operation">{option}</div>
                              <div className="description">{doc(option).description}</div>
                            </div>)}
                  </div>
                </div>);
    }
    let d = doc(zobject.name);
    if (d) {
        let tag = d.tags;
        if (Array.isArray(tag)) {
            tag = tag[0];
        } else if (!tag) {
            tag = "Object";
        }

        if (zobject.plugin) {
            tag = "Plugin";
        }

        let params = {... d.params};
        if (Array.isArray(d.inlets)) {
            params = {};
            d.inlets.forEach(
                inlet => params[inlet] = d.params[inlet] || "");
        }
        let operations = {... d.operations};

        let actions = Object.keys(zobject.actions);
        return (
            <div className="zobject-tag single-object">
              {/*<div onClick={sendBang} className="bang-btn">send bang</div>*/}
              <div className="tag-name">{tag}</div>
              <div className="tag-options">
                <div className="tag-option">
                  <div className="operation">{zobject.plugin ? zobject.plugin.name : zobject.name}</div>
                  <div className="description">{zobject.plugin ? "" : d.description}</div>
                  <div className="args">
                    {Object.keys(params).map(
                        paramName =>
                        <div className="param">
                          <img className="operation-icon" src="/operation-icon.svg"/>
                          <div className="param-name">
                            {paramName}
                          </div>
                          <div className="param-description">
                            {params[paramName]}
                          </div>
                        </div>)}
                  </div>
                </div>
              </div>
              {!zobject.plugin && zobject.name === "subpatch" && <SubPatchTools zobject={zobject}/>}
              {operations && Object.keys(operations).length > 0 &&
              <div className="tag-name">
                Operations
                <div className="operation-help-text">
                  (via left-inlet)
                </div>
              </div>}
              {Object.keys(operations).length > 0 && <div className="tag-options">
                <div className="tag-option">
                  <div className="operations">
                    {Object.keys(operations).filter(
                        operation => operation[operation] && operation[operation].parameters)
                     .map(
                          operation =>
                        <div className="operation-option">
                          <img className="operation-icon" src="/operation-icon.svg"/>
                          <div className="operation-and-description">
                            <div className="operation">
                              <div onClick={() => onClickOperation(operation, operations[operation].parameters)} className="operation-btn">{operation}</div>{operations[operation].parameters.map(x => <span className="operation-arg">{x}</span>)}
                            </div>
                            <div className="description">
                              {operations[operation].description}
                            </div>
                          </div>
                        </div>)}
                    </div>
                  </div>
                                                     </div>}
              {d.connects && <ObjectConnector zobject={zobject} connects={d.connects}/>}
              <AttributesInspector zobject={zobject}/>
              {/* {actions.length > 0 && <ZObjectActions zobject={zobject} actions={actions}/>} */}
              <OperationDocs zobject={zobject}/>
            </div>);
    }
    return (<div></div>);
}

const mapStateToProps = (state, props) => {
  return {
      name: props.zobject.name,
      inputText: state.spatial.zobjectInputText
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZObjectTools);
