import React, { useEffect, useCallback, useState} from 'react';
import {useWindowSlider} from "components/hooks/useWindowSlider.js";
import {connect} from 'react-redux';

const WarpKnob = ({track, warp, bpm, warpLength, warpFactor}) => { 
    let resolvedTempo = Math.round((bpm / warpLength)*10)/10.0;
    
    const setValue = useCallback((value) => {
        let warpLength = bpm / value;
        warp.warpLengthParameter.set(warpLength, false);
    }, [warp, bpm]);

    const down = useCallback(() => {
        warp.warpFactorParameter.set(warpFactor * 2, false);
    }, [warpFactor, warp]);

    const up = useCallback(() => {
        warp.warpFactorParameter.set(warpFactor / 2, false);
    }, [warpFactor, warp]);

    const {onMouseDown, editing} = useWindowSlider({min: 45, max: 200, setValue, value: resolvedTempo});

    return [
        <div onMouseDown={onMouseDown} className={editing ? "warp-knob editing" : "warp-knob"}>
          <span className="warp-tempo">{resolvedTempo}</span> bpm
        </div>,
        <div className={"warp-factor"}>
          <div onClick={up} className="arrow-up"/>
          <div className="factor-value">{2 / warpFactor}x</div>
          <div onClick={down} className="arrow-down"/>
        </div>
    ];
};

const mapStateToProps = (state, props) => {
  return {
      bpm: props.track.sequencerEngine.bpm,
      warpLength: props.warp ? props.warp.warpLength : 1,
      warpFactor: props.warp ? props.warp.warpFactor: 2
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(WarpKnob);
