import React, { useEffect, useCallback, useState} from 'react';
import {
    setObjectDragging,
    setSelectedZObject} from 'actions/index.js';
import {connect} from 'react-redux';
import {BANG, LIST, SYMBOL, NUMBER} from 'zobject/data-types.js';
import NestedZObject from "components/v3/sidebar/NestedZObject.js"

const ObjectTree = ({
    setSelectedZObject,
    setObjectDragging,
    zobjects,
    zmessages,
    selectedZObject,
    subpatch,
}) => { 

    const onClickZObject = (obj) => {
        setSelectedZObject(obj);
    };

    return (<div
              style={{height: subpatch.height - 120, maxHeight: subpatch.height - 120}}
              className="object-tree">
              {zmessages.map(
                  x => <div
                         draggable={true}
                         onDragStart={(e) => {
                             e.dataTransfer.setData('text/html', x.id); //cannot be empty string
                             setObjectDragging(x);
                         }}
                         onDragOver={e => e.preventDefault()}
                         key={x.id}
                         onClick={() => setSelectedZObject(x)}
                         className={x === selectedZObject ? "selected zmessage" : "zmessage"}>
                         <img className="object-icon" src={
                             x.type === NUMBER ?
                                 "/znumber-light.svg" : "/zmessage-light.svg"}
                         /> {typeof x.storedMessage === "string" ?
                             x.storedMessage : x.type === NUMBER ?
                             "Number" : "Message"}
                       </div>)}
              {zobjects.map(
                  x => <NestedZObject
                         key={x.id}
                         zobject={x}
                         searchTerm={""}
                         setSelectedZObject={onClickZObject}
                         setObjectDragging={setObjectDragging}
                         selectedZObject={selectedZObject}/>)}
            </div>);
};

const mapStateToProps = (state, props) => {
  return {
      selectedZObject: state.zobject.selectedZObject,
      zobjects: props.subpatch.zObjects,
      zmessages: props.subpatch.zMessages,
      objectDragging: state.zobject.objectDragging
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSelectedZObject: x => dispatch(setSelectedZObject(x)),
      setObjectDragging: x => dispatch(setObjectDragging(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ObjectTree);
