import React, { useRef, useEffect, useCallback, useState} from 'react';
import AudioUnit from 'engine/audio.js';
import Tone from 'tone';
import {setSampleDragging} from 'actions/index.js';
import {connect} from 'react-redux';
import './recent.scss';
import Colors from 'components/Colors.js';
const colors = new Colors();

const RecentlyAddedSounds = ({recentlyAddedSounds, setSampleDragging}) => { 
    let audioUnit = useRef(new AudioUnit(Tone.Master.context));
    let [selected, setSelected] = useState(undefined);

    const playSound = useCallback((sample) => {
        audioUnit.current.setBuffer(sample.audioBuffer);
        audioUnit.current.playSound(0);
        setSelected(recentlyAddedSounds.indexOf(sample));
    }, [recentlyAddedSounds, setSelected]);

    if (recentlyAddedSounds.length === 0) {
        return '';
    }
    return (
        <div className="recently-added-sounds">
        {recentlyAddedSounds.map(
            (sample, i) =>
            <div
              draggable={true}
              onDragOver={e => e.preventDefault()}
              onDragStart={() => setSampleDragging(sample)}
              onClick={(x) => playSound(sample)}
              className={i === selected ? "recent-sound selected" : "recent-sound"}>
              <div
                style={{backgroundColor: colors.getColorForHash(sample.ipfsHash)}}
                className="recent-thumb"/>
              <div className="recent-name">{sample.name}</div>
            </div>)}
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
      recentlyAddedSounds: state.samples.recentlyAddedSounds
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setSampleDragging: x => dispatch(setSampleDragging(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RecentlyAddedSounds);
