import React, { useRef, useEffect, useCallback, useState} from 'react';
import UnknownProgressBar from "components/progressbar/UnknownProgressBar.js";
import {connect} from 'react-redux';
import SamplesStreamExplorer from "components/sidebar/SamplesStreamExplorer.js";

const TIMEOUT = 700;

const SampleSearcher = ({searchText, sequencerEngine}) => { 
    let {auditionTrack} = sequencerEngine;
    let counter = useRef(0);
    let [stream, setStream] = useState(auditionTrack.samplesStream);
    let [searching, setSearching] = useState(false);

    useEffect(() => {
        if (!searchText) {
            return;
        }
        counter.current++;
        setSearching(true);
        let _counter = counter.current;
        setTimeout(() => {
            if (_counter !== counter.current) {
                return;
            }
            sequencerEngine.searchForSounds([searchText], true, auditionTrack, false, false).then(
                x => {
                    setSearching(false);
                    setStream(auditionTrack.samplesStream.sounds);
                });
        }, TIMEOUT);
    }, [setStream, searchText, setSearching]);
    
    if (searching) {
        return (<UnknownProgressBar text="Searching..." width={230}/>);
    }
    return (<SamplesStreamExplorer track={auditionTrack} sequencerEngine={sequencerEngine}></SamplesStreamExplorer>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SampleSearcher);
