import {useCallback} from 'react';
import {setTrackDragging} from 'actions/index.js';
import {
    setWindowPosition,
    setPulledEffects,
    setZObjectTypeDragging} from 'actions/index.js';

export const useModuleDrop = ({subSequence, setSelection, getXY, createToggle, createSequencer, createPad, createZObject, createZMessage, createZNumber, createButton}) => {
    const onDrop = useCallback((e) => {
        let {x,y} = getXY(e);
        let {sequencerEngine} = subSequence;
        let {state} = sequencerEngine;
        let {trackDragging} = state.tracks;
        let {zobjectTypeDragging} = state.zobject;
        if (zobjectTypeDragging) {
            switch(zobjectTypeDragging) {
            case "zobject":
                createZObject(x,y);
                break;
            case "zmessage":
                createZMessage(x,y);
                break;
            case "zpad":
                createPad(x,y);
                break;
            case "zbutton":
                createButton(x,y);
                break;
            case "ztoggle":
                createToggle(x,y);
                break;
            case "znumber":
                createZNumber(x,y);
                break;
            case "sequencer":
                createSequencer(x,y);
                break;
            case "zcomment":
                createZObject(x,y, "comment");
                break;
            default:
                let tokens = zobjectTypeDragging.split(" ");
                let number = parseInt(tokens[1]);
                let pulledEffects = sequencerEngine.state.effects.pulledEffects;
                let type = undefined;
                if (tokens[0] === "MODULE_ENVELOPE") {
                    // drag envelope
                    let envelope = subSequence.envelopes.find(x => x.number === number);
                    console.log("envelope =", envelope);
                    type = {
                        type: "MODULE_ENVELOPE",
                        id: envelope.id,
                        subType: envelope.number,
                        instrument: envelope
                    };
                } else {
                    // drag lfo
                    let lfo = subSequence.lfos.find(
                        ({lfo}) => lfo.number === number);
                    type = {
                        type: "LFO",
                        id: lfo.id,
                        effect: lfo,
                        instrument: lfo
                    };
                }
                sequencerEngine.dispatch(setWindowPosition(
                    type.id, {x,y}));
                sequencerEngine.dispatch(
                    setPulledEffects([... pulledEffects, type]));
            }
            
            sequencerEngine.dispatch(setZObjectTypeDragging(undefined));
            if (setSelection) {
                setSelection(undefined);
            }
        }

        /*
        if (trackDragging) {
            // need to switch the track over
            if (trackDragging.subSequence !== subSequence) {
                trackDragging.subSequence.deleteTrackFromPads(trackDragging);
                trackDragging.subSequence.groups.deleteTrack(trackDragging);
                trackDragging.subSequence.tracks = trackDragging.subSequence.tracks.filter(
                    t => t !== trackDragging);
                subSequence.tracks.push(trackDragging);
                subSequence.groups.addTrack(trackDragging);
                trackDragging.subSequence = subSequence;
            }
            sequencerEngine.dispatch(setTrackDragging(undefined));
        }
        */
    }, [subSequence]);

    return onDrop;
};
