import React, {useState, useEffect, useCallback} from 'react';

export const useSubPatchScroll = ({ref, subpatch, size, setSize}) => {
    let [styleSize, setStyleSize] = useState(size);
    useEffect(() => {
        ref.current.addEventListener("mousewheel", onWheel, {passive: false});
        return () => {
            if (ref && ref.current) {
                ref.current.removeEventListener("mousewheel", onWheel, {passive: false});
            }
        };
    }, [styleSize, setStyleSize]);

    const onWheel = useCallback((e) => {

        if (new Date().getTime() - window.lastScrollTime < 1000) {
            // e.preventDefault();
            return false;
        }
        if (!ref || !ref.current) {
            return false;
        }

        e.stopPropagation();
        e.preventDefault();

        let {scrollTop, scrollLeft} = ref.current;

        let {deltaX, deltaY} = e;

        let x = scrollLeft + deltaX;
        let y = scrollTop + deltaY;

        if (!size) {
            return false;
        }

        let sx = 0;
        let sy = 0;
        size = {
            width: Math.max(styleSize.width, window.innerWidth - sx),
            height: Math.max(styleSize.height, window.innerHeight - sy)
        };
                
        if (size.width < 200 || size.height < 200) {
            return;
        }
        let _size = {width: deltaX + size.width, height: deltaY + size.height};
        setStyleSize(_size);
        //sizeRef.current = _size;
        ref.current.scrollTo(x,y);
        return false;
    },[
        styleSize,
        setStyleSize,
      ]);
};
