import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {setTrackDragging} from 'actions/index.js';
import './synth.scss';
const SynthThumbnail = ({track}) => { 
    let color = track.color;
    let style = {
        backgroundColor: color
    };
    return (<div
              draggable={true}
              onDragStart={() => setTrackDragging(track)}
              onDragOver={(e) => e.preventDefault()}
              className="youtube-info synth-info">
              <div className="synth-thumbnail sound-thumbnail">
                <div style={style} className="white-key"/>
                <div style={style} className="white-key"/>
                <div style={style} className="white-key"/>
                <div className="black-key key-1"/>
                <div className="black-key key-2"/>
              </div>
            </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setTrackDragging: x=>dispatch(setTrackDragging(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SynthThumbnail);
