import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';

const ZObjectActions = ({actions, zobject}) => { 
    return (
        <div className="zobject-actions">
          <div className="label">Actions</div>
        {actions.map(
            x => <div
                   onClick={e => zobject.executeAction(x)}
                   className="action-option">
                   <div className="pushable dot"/>
                   {x}</div>)}
        </div>
    );
};

