import React, { useRef, useEffect, useCallback, useState} from 'react';
import {isNumber} from "utils/IsNumber.js"
import ZNumber from "components/zobject/ZNumber.js";
import {setObjectStyling} from 'actions/index.js';
import {ChromePicker} from 'react-color';
import {connect} from 'react-redux';

const AttributesInspector = ({zobject, setObjectStyling}) => { 
    let [style, setStyle] = useState(zobject.attributes || {'': ''});
    let buttonRef = useRef(zobject);
    let [focus, setFocus] = useState(undefined);

    useEffect(() => {
        buttonRef.current = zobject;
    }, [zobject]);

    useEffect(() => {
        setStyle(zobject.attributes || {'': ''});
    }, [zobject, setStyle]);
    
    useEffect(() => {
        buttonRef.current.attributes = {... style};
        if (!zobject.sequencerEngine.isLoading) {
            setObjectStyling(buttonRef.current.id, {... style});
        }
    }, [style]);

    let [showingColor, setShowingColor] = useState(undefined);

    return (
        <div className="attributes-inspector">
          <div className="label">
            Attributes
          </div>
          {Object.keys(style).filter(x=> x !== "").map(
             (key, i) =>
             <Styler
               showingColor={showingColor === i}
               key={i}
               skey={key}
               style={style}
               zobject={zobject}
               setShowingColor={(x) => setShowingColor(x === false ? undefined : i)}
               setFocus={setFocus}
               i={i}
               focus={focus}
               value={style[key]}
               setStyle={setStyle}
             />)}
        </div>
    );
};

const Styler = ({zobject, setShowingColor, showingColor, i, focus, setFocus, skey, value, setStyle, style}) => {
    let ref = useRef(undefined);
    let [min, setMin] = useState(-2*Math.abs(parseFloat(value)));
    let [max, setMax] = useState(2*Math.abs(parseFloat(value)));

    useEffect(() => {
        if (i === focus) {
            setTimeout(() => {
                ref.current.focus();
            }, 10);
        }
    }, [i, focus]);

    const onChangeKey = useCallback((e) => {
        let text = e.target.value;
        let _style = {... style};
        delete _style[skey];
        _style[text] = value;
        _style[''] = '';
        setStyle(_style);
    }, [style, setStyle, skey, value]);

    const onChangeValue= useCallback((e, isNum) => {
        let text = e.target.value;
        if (isNumber(text)) {
            text = parseFloat(text);
        }
        let _style = {... style};
        _style[skey] = text;
        setStyle({... _style});
        zobject.setAttribute(skey, text);
        if (text === 0) {
            text = 1;
        }
        if (isNum) {
            setMin(-2*Math.abs(parseFloat(text)));
            setMax(2*Math.abs(parseFloat(text)));
        }
    }, [style, setStyle, skey, setMax, setMin]);

    if (skey === "") {
        return '';
    }

    let inner = '';
    if (typeof value === "boolean") {
        // we need a checkbox here
        inner = (
            <input
              className="boolean-checkbox"
              type="checkbox"
              onChange={(e) => onChangeValue({target: {value: !value}})}
              checked={value}
            ></input>
        );
    } else if (isNumber(value)) {
        inner = (
            <div className="number-and-input">
              <ZNumber
                hideNumber={true}
                znumber={{attributes: {}, max, min}} onChange={(x) => onChangeValue({target: {value: x}})} number={value} round={false}/>
              <input
                className="style-value-input"
                value={value}
                type="text"
                onChange={(e) => onChangeValue(e, true)}/>
            </div>
        );
    } else {
        inner = (
            <input
              className="style-value-input"
              value={value}
              type="text"
              onChange={onChangeValue}/>
        );
    }

    return (
        <div className="styler">
          {skey==="Include in Presentation" ?
           <div className="icon-wrapper">
             Include in <img className="presentation-icon" src="/presentation-mode.svg"/></div> : <input
            readOnly
            onFocus={() => setFocus(i)}
            ref={ref} value={skey} type="text" onChange={onChangeKey}/>}
          <div className="style-divider">:</div>
          {((skey && skey.includes("color")) || (value && value.startsWith && value.startsWith("#"))) ?
           <div
             onClick={() => setShowingColor(!showingColor)}
             style={{backgroundColor: value}} className="zcolor-btn">
             {showingColor && <div style={{cursor: 'unset'}} onClick={e => e.stopPropagation()}>
              <ChromePicker onChange={(x) => onChangeValue({
                 target: {value: x.hex}})} color={{hex: value}}/>
                              </div>}
           </div> : ""}
          {inner}
        </div>
    );
};


const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setObjectStyling: (id, styling) => dispatch(setObjectStyling(id, styling))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AttributesInspector);
