import React, { useRef, useEffect, useCallback, useState} from 'react';
import RecentlyAddedSounds from './recent/RecentlyAddedSounds.js';
import PluginsViewer from "components/v3/sidebar/plugins/PluginsViewer.js"
import TrackParameters from "./track/TrackParameters.js";
import TrackInformation from "components/v3/sidebar/track/TrackInformation.js";
import InstrumentViewer from './track/InstrumentViewer.js';
import {EFFECT, ZOBJECT, ZMESSAGE, PAD, TRACK} from 'constants/object-types.js';
import ObjectInformation from "components/v3/sidebar/ObjectInformation.js";
import Sends from './Sends.js';
import {
    setShowTrackInstrument,
    setSelectedZObject,
    setRightSidebarOption} from 'actions/index.js';
import QuickSampleChooser from "components/sidebar/QuickSampleChooser.js";
import ToolsSidebar from './ToolsSidebar.js';
import AuditionSidebar from './audition/AuditionSidebar.js';
import Navigator from './Navigator.js';
import {connect} from 'react-redux';
import './sidebar.scss';

const RightSidebar = ({
    setSelectedZObject,
    setShowTrackInstrument,
    secondaryZObject,
    show,
    sequencerEngine,
    position,
    selectedZObject,
    selectedTrack,
    selectedSteps,
    setOption,
    option,
    showPlugins,
    showTrackInstrument
}) => { 
    let ref = useRef();

    useEffect(() => {
        if (selectedSteps && selectedSteps.length > 0) {
            setOption("Tools");
        }
    }, [setOption, selectedSteps]);

    useEffect(() => {
        if (selectedZObject && selectedZObject.isFrozen) {
            setOption("Tools");
        }
    }, [setOption, selectedZObject]);

    useEffect(() => {
        if (!selectedZObject) {
            setShowTrackInstrument(undefined);
        }
    }, [selectedZObject]);

    useEffect(() => {
        window.addEventListener("keydown", onKey);
        return () => window.removeEventListener("keydown", onKey);
    }, [setOption, option]);

    const onKey = useCallback((e) => {
        if (e.target.tagName === 'INPUT' ||
            e.target.tagName === 'TEXTAREA') {
            return;
        }
            
        if (e.key === "Tab" && e.shiftKey) {
            setOption(option === "Tools" ? "Audition" : "Tools");
        }
    }, [setOption, option]);

    if (!show) {
        return '';
    }


    let className = selectedZObject ? "right sidebar " + option : "right sidebar";
    if (showPlugins) {
        return (
            <div ref={ref} className={className}>
              <PluginsViewer sequencerEngine={sequencerEngine}/>
            </div>
        );
    }
    return [
        showTrackInstrument && selectedZObject && selectedZObject.getObjectType() === TRACK &&
            <InstrumentViewer track={selectedZObject}/>,
        <div ref={ref} className={className}>
          {selectedZObject && <Navigator
                                zobject={selectedZObject}
                                showSave={option === "Audition"}
                                option={option} setOption={setOption}
                                options={
                                  selectedZObject && selectedZObject.isFrozen ? 
                                        ["Tools"] : 
                                    ["Tools", "Audition"]}/>}
          {selectedZObject && <ObjectInformation object={selectedZObject}/>}
          {sequencerEngine && !selectedZObject && <QuickSampleChooser sequencerEngine={sequencerEngine}/>}
          {selectedZObject && option === "Tools" ? <ToolsSidebar object={selectedZObject} sequencerEngine={sequencerEngine}/>
           : selectedZObject && <AuditionSidebar object={selectedZObject} sequencerEngine={sequencerEngine}/>}
          <div
            onClick={() => setSelectedZObject(selectedTrack)}
            className="mini-track-information">{sequencerEngine && !selectedZObject && selectedTrack &&
           <TrackInformation track={selectedTrack}/>}
          </div>
          {sequencerEngine && !selectedZObject 
           && <RecentlyAddedSounds sequencerEngine={sequencerEngine}/>}
          {sequencerEngine && !selectedZObject && sequencerEngine.tracks &&
           sequencerEngine.tracks.length > 0 && <Sends sequencerEngine={sequencerEngine}/>}
          {secondaryZObject && <ObjectInformation object={secondaryZObject}/>}
        {secondaryZObject && option === "Tools" ? <ToolsSidebar object={secondaryZObject} sequencerEngine={sequencerEngine} /> : ''}
        </div>
    ];
}

const mapStateToProps = (state, props) => {
    let selected = state.zobject.selectedZObject;
    return {
        selectedZObject: selected,
        show: state.spatial.sidebarsShowing,
        selectedSteps: state.keyboard.selectedPianoRollSteps,
        showTrackInstrument: state.spatial.showTrackInstrument,
        secondaryZObject: state.zobject.secondaryZObject,
        option: state.audition.rightSidebarOption,
        showPlugins: state.plugins.showPlugins,
        selectedTrack: props.sequencerEngine && props.sequencerEngine.getSelectedTrack(),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setOption: x => dispatch(setRightSidebarOption(x)),
      setSelectedZObject: x => dispatch(setSelectedZObject(x)),
      setShowTrackInstrument: x => dispatch(setShowTrackInstrument(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RightSidebar);
