import React, { useRef, useEffect, useCallback, useState} from 'react';
import ModuleOption from './ModuleOption.js';
import {connect} from 'react-redux';
import '../../ModuleLoader.scss';
import {useScroll} from 'components/selector2/useScroll.js';
import {getProjects} from 'components/sequences2/getProjects.js';

const ModuleLoader = (props) => { 
    let [clicked, setClicked] = useState(false);
    let [value, setValue] = useState('');
    let [projects, setProjects] = useState([]);
    const ref = useRef(undefined);
    let {size} = useScroll(ref, 16, true, value);

    useEffect(() => {
        if (clicked) {
            getProjects(value).then(
                setProjects);
        }
    }, [clicked, setProjects, value]);

    if (!clicked) {
        return (<div>
                  <div
                    onClick={() => props.sequencerEngine.newSubSequence()}
                    className="needs-click module-loader new-module">
                    <div className="click-me">
                      New Module
                    </div>
                  </div>
                  <div
                    onClick={() => setClicked(true)}
                    className="needs-click module-loader">
                    <div className="click-me">
                      Click to load modules
                    </div>
                  </div>
                </div>
               );
    }
    return (
        <div className="module-loader">
          <input type="text" placeholder="Search for modules" value={value} onChange={e => setValue(e.target.value)}/>
          <div ref={ref} className="module-list">
            {projects.filter(p => p.guildId === 0).slice(0, size).map(
                p => <ModuleOption
                       key={p.newSequence}
                       sequencerEngine={props.sequencerEngine} project={p}/>)}
          </div>
        </div>
    );
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ModuleLoader);
