import React, { useRef, useEffect, useCallback, useState} from 'react';
import {
    setUXLockObject,
    setUXHideObject
} from 'actions/index.js';
import {useContextMenu} from "components/dropdowns/useContextMenu.js"
import ZObjectContextMenu from "components/zobject/ZObjectContextMenu.js";
import {connect} from 'react-redux';

const ObjectOption = ({
    setObjectDragging,
    hidden,
    locked,
    setUXLockObject,
    setUXHideObject,
    className,
    setShow,
    setSelectedZObject,
    show,
    zobject
}) => { 

    let ref = useRef();
    const {onContextMenu, dropdownPos, setDropdownPos} = useContextMenu({ref, useTop: false});

    useEffect(() => {
        if (dropdownPos) {
            setSelectedZObject(zobject);
        }
    }, [dropdownPos, zobject]);

    if (dropdownPos) {
        className += " showing-context-menu";
    }
    return (
      <div
        ref={ref}
        onDragLeave={e => e.preventDefault()}
        draggable={true}
        onDragStart={(e) => {
            e.dataTransfer.effectAllowed = "copy";
            e.dataTransfer.setData('text/html', zobject.id); //cannot be empty string
            setObjectDragging(zobject);
        }}
        onDragOver={e => e.preventDefault()}
        onContextMenu={onContextMenu}
        className={className}>
        {dropdownPos &&
         <ZObjectContextMenu
           dropdownPos={dropdownPos}
           setDropdownPos={setDropdownPos}
           zobject={zobject}/>
        }
        <div onClick={() => setShow(!show)} className="arrow-container">
          <div className={show ? "arrow-down" : "arrow-right"}/>
        </div>
        <img className="object-icon" src="/zobject-light.svg"/>
        <div
          onClick={() => setSelectedZObject(zobject)}
          className="zobject-name">
          <span className="object-name">{zobject.name}</span>
          <span className="object-arg">{typeof zobject.args[0] === "string" && zobject.args[0]}</span>
       </div>
        <img
          onClick={() => setUXHideObject(zobject.id, !hidden)}
          className={hidden ? "hidden eye" : "eye"}
          src={hidden ? "/eyeball-hidden.svg" : "/eyeball.svg"}
        />
        <img
          onClick={() => setUXLockObject(zobject.id, !locked)}
          className="lock-ux-btn"
          src={locked ? "/zlock-locked-light.svg" : "/zlock-unlocked-light.svg"}
        />
      </div>);
}

const mapStateToProps = (state, props) => {
    let id = props.zobject.id;
    return {
        hidden: state.spatial.hiddenObjects[id],
        locked: state.spatial.lockedObjects[id]
    };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setUXLockObject: (id, x) => dispatch(setUXLockObject(id, x)),
      setUXHideObject: (id, x) => dispatch(setUXHideObject(id, x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ObjectOption);
