import React, { useEffect, useCallback, useState} from 'react';
import Colors from 'components/Colors.js';
import {connect} from 'react-redux';

const colors = new Colors();

const ParameterResult = ({
    i,
    mouseDown,
    selectedParameters,
    setSelectedParameters,
    higherOrderSequencer,
    parameter,
    setParameters,
}) => { 
    let {context} = parameter;
    let {lfosTrack, track, instrument, effect, midiEffect, subDevice} = context;
    let subSequence = track && track.subSequence;
    if (lfosTrack) {
        subSequence = lfosTrack.subSequence;
    }

    const onClick = useCallback(() => {
        higherOrderSequencer.setParameter(parameter);
        setParameters([... higherOrderSequencer._parameters])
    }, [parameter]);

    const onMouseOver = useCallback((e) => {
        if (selectedParameters && mouseDown.current) {
            setSelectedParameters([... selectedParameters, i]);
        }
    }, [i, selectedParameters, setSelectedParameters]);

    if (track && track.isMetronome) {
        return '';
    }
    if (context.formant && !context.formant.enabled) {
        return '';
    }

    let sorted = selectedParameters && [...selectedParameters].sort();
    let className = "parameter-result";
    if (sorted) {
        if (selectedParameters.includes(i)) {
            className += " selected";
        }
    }
    return (
        <div
          onMouseOver={onMouseOver}
          onMouseDown={(e) => e.metaKey && selectedParameters ? setSelectedParameters([... selectedParameters, i]) : setSelectedParameters([i])}
          onClick={onClick} className={className}>
          {subSequence &&
           <div
             style={{backgroundColor: subSequence.color}}
             className="p-sub-sequence">{subSequence.name}</div>}
          <div className="p-name">
            {parameter.name}
          </div>
          {track &&
           <div style={{backgroundColor: colors.getColorForTrack(track)}}
                className="p-track">{track.trackNumber + 1}. {track.getName()}</div>}
          {subDevice && <div
                          style={{backgroundColor: colors.getColorForHash(track ? track.getName() + subDevice.name : subDevice.name)}}
                          className="p-sub-device">{subDevice.name}</div>}
          {effect && <div className="p-effect">{effect.name}</div>}
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ParameterResult);
