import React, { useEffect, useCallback, useState} from 'react';
import {setHigherOrderStepsSelected} from 'actions/index.js';
import {connect} from 'react-redux';

const RoutingMatrix = ({orientation, higherOrderSequencer, setHigherOrderStepsSelected}) => { 
    let routings = higherOrderSequencer.routings[orientation];
    let modules = higherOrderSequencer.modules;

    const isConnected = useCallback((from, to) => {
        return routings[from.id] && routings[from.id].includes(to);
    }, [routings]);

    const toggle = useCallback((from, to) => {
        if (isConnected(from, to)) {
            higherOrderSequencer.disconnectModules(orientation, from, to);
        } else {
            higherOrderSequencer.connectModules(orientation, from, to);
        }
    }, [routings]);

    const toggleFreeform = useCallback((module) => {
        if (higherOrderSequencer.isFreeform(module)) {
            higherOrderSequencer.disconnectFromFreeform(module);
        } else {
            higherOrderSequencer.connectToFreeform(module);
        }
    }, []);

    return (
        <div className="orientation-routing">
          <div className="out-label">
            OUTPUT
          </div>
          <div className="module-outs">
            {modules.map(
                module =>
                    <div key={module.id}>
                      <div className="m-name">{module.name}</div>
                      <div className="out-line"/>
                    </div>
            )}
            <div>
              <div className="m-name">free</div>
              <div className="out-line"/>
            </div>
          </div>
          <div className="m-row">
            <div className="in-label">
              INPUT
            </div>
            <div>
            {modules.map(
                module =>
                    <div
                      key={module.id}
                      className="m-routing">
                      <div className="m-name">
                        {module.name}
                      </div>
                      <div className="in-line"/>
                      {modules.map(
                          toModule => 
                              <div
                                key={toModule.id}
                                onClick={() => toggle(module, toModule)}
                                className=
                                  {isConnected(module, toModule) ?
                                   "m-option connected" : "m-option"}/>)}
                      <div
                        onClick={() => toggleFreeform(module)}
                        className={higherOrderSequencer.isFreeform(module) ?
                                   "m-option connected" : "m-option"}/>
                    </div>)}
            </div>
          </div>
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
      orientationRoutings: state.higherOrder.orientationRoutings,
      modulesLen: props.higherOrderSequencer.modules.length
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setHigherOrderStepsSelected: x=>dispatch(setHigherOrderStepsSelected(x))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RoutingMatrix);
