import React, { useRef, useEffect, useCallback, useState} from 'react';
import ParameterResult from './ParameterResult.js';
import {index} from "parameters/ParameterIndex.js";
import {connect} from 'react-redux';

const ParameterSearcher = ({higherOrderSequencer, setParameters}) => { 
    const [value, setValue] = useState('');
    const [results, setResults] = useState([]);
    const [selectedParameters, setSelectedParameters] = useState(undefined);
    const mouseDown = useRef(false);
    
    useEffect(() => {
        if (value !== "") {
            setResults(
                index.search(value)
                    .filter(
                        p => p.audioParam || p.audioParams));
        } else {
            setResults([]);
        }
    }, [value, setResults]);

    useEffect(() => {
        setSelectedParameters(undefined);
    }, [results, setSelectedParameters]);

    const createMacro = useCallback(() => {
        let params = Array.from(new Set(selectedParameters.map(
            x => results[x])));
        let macro = higherOrderSequencer.createMacro(params);
        setSelectedParameters(undefined);
        setParameters([macro.macroParameter])
    }, [selectedParameters, results, setSelectedParameters])

    const onChange = useCallback((e) => {
        setValue(e.target.value);
    }, [setValue]);
    
    return (
        <div
          onMouseDown={() => mouseDown.current=true}
          onMouseUp={() => mouseDown.current=false}
          onMouseLeave={() => mouseDown.current=false}
          className="parameter-searcher">
          <input placeholder="Search for parameters" className="parameter-searcher" value={value} type="text" onChange={onChange}/>
          <div onClick={() => setValue('')} className="delete-btn">
            x
          </div>
          <div className="parameter-results">
            {results
             .filter(
                 p =>
                     !higherOrderSequencer._parameters.includes(p)) // dont show stuff weve already mapped
             .filter(p => !p.hasMacro) // dont show macros 
             .map(
                (r, i) => <ParameterResult
                            mouseDown={mouseDown}
                            selectedParameters={selectedParameters}
                            setSelectedParameters={setSelectedParameters}
                            i={i}
                            setParameters={setParameters}
                            higherOrderSequencer={higherOrderSequencer}
                            key={r.id + '-' + i} parameter={r}/>)}
          </div>
          {selectedParameters && <div
                                   onClick={createMacro}
                                   className="create-macro-btn">Create Macro ({selectedParameters.length})</div>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ParameterSearcher);
