import React, { useEffect, useCallback, useState} from 'react';
import FreezeHistory from "components/v2/FreezeHistory.js";
import {SAMPLER, MULTI_FREEZE} from 'constants/track-types.js';
import StepGroupEditor from "components/sidebar/StepGroupEditor.js";
import {connect} from 'react-redux';
import ParamValue from './ParamValue.js';
import TrackFrozenComponent from "components/v2/TrackFrozenComponent.js";
import Colors from 'components/Colors.js';
const colors = new Colors();

const PARAMS = ["transpose", "duration", "velocity", "attack", "release"];

const TrackParameters = ({track, isFrozen, selectedSteps, stepParameterSelected}) => { 
    if (track.isAudioTrack) {
        return '';
    }
    if (isFrozen) {
        return (<div>
                  <TrackFrozenComponent
                    track={track}
                    trackColor={colors.getColorForTrack(track)}
                    isTrackFrozen={true}/>
                
                  {track.getTrackType() === MULTI_FREEZE && <div className="freeze-history">
                                                              <FreezeHistory track={track}/>
                                                            </div>}
               </div>
               );
    }

    
    let groups = !selectedSteps ? [] :
        Array.from(new Set(selectedSteps.map(s => s && s.group).filter(s => s)));
    return (<div className="track-parameters">
              {PARAMS.map(
                  param =>
                      <ParamValue
                        isSelected={stepParameterSelected && param === stepParameterSelected.toLowerCase()}
                        rounded={param === "transpose"}
                        track={track}
                        parameters={selectedSteps && selectedSteps.length > 0
                                    ?
                                    selectedSteps.filter(x => x).map(x => x[param + "Parameter"]) :
                                    track && track.trackData ? [track.trackData[param + "Parameter"]] : []}/>
              )}
            {selectedSteps && selectedSteps.length > 0 &&
              <div className="steps-indicator">
               <div className="selected-type">Steps</div>
               <div className="selected-length">{selectedSteps.length}</div>
              </div>}
              {groups.map(group => <StepGroupEditor
                                     track={track}
                                     key={group.id} sequencerEngine={track.sequencerEngine} group={group}/>)}
            </div>);
}

const mapStateToProps = (state, props) => {
  return {
      isFrozen: props.track.isFrozen,
      selectedSteps: state.keyboard.selectedPianoRollSteps,
      stepParameterSelected: state.cirklon.stepParameterSelected
  };
};

const mapDispatchToProps = (dispatch) => {
  return {

  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TrackParameters);
