import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import './subpatch.scss';

const SubPatchTools = ({zobject}) => { 
    let [name, setName] = useState(zobject.args[0]);

    const onChange = useCallback((e) => {
        let val = e.target.value.replaceAll(" ", "-");
        zobject.text = "subpatch " + val;
        setName(val);
    }, [zobject, setName]);
    return (
        <div className="subpatch-tools">
          <div className="label">Name</div>
          <input value={name} onChange={onChange} type="text" className="subpatch-name-input"/>
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubPatchTools);
