import React, { useRef, useEffect, useCallback, useState} from 'react';
import LockButton from "components/LockButton.js";
import {freezePatterns} from 'components/v2/trackRightClickActions.js';
import WarpKnob from './WarpKnob.js';
import WarpButton from "components/WarpButton.js";
import {setShowTrackInstrument} from 'actions/index.js';
import DetailsView from "components/DetailsView.js"
import {AUDIO_FX, MIDI_FX} from 'constants/effect-types.js';
import {
    setPulledEffects,
    setSelectedMixEffect,
} from 'actions/index.js';
import {getInstrumentComponent} from "components/effects/getInstrumentComponent.js";
import {connect} from 'react-redux';
import './instrument.scss';

const TrackQuickInstrument = ({
    isFrozen,
    locked,
    track,
    isWarped,
    show,
    setShow,
    clearSelectedEffect,
    setPulledEffects,
    planar
}) => { 
    let ref = useRef();
    let wrapperRef = useRef();

    const flip = useCallback(() => {
        setShow(!show);
    }, [show, setShow]);

    useEffect(() => {
        if (show) {
            clearSelectedEffect(AUDIO_FX);
            clearSelectedEffect(MIDI_FX);
        }
    }, [show]);

    const pullPlanar = useCallback(() => {
        let pulledEffects = track.state.effects.pulledEffects || [];
        setPulledEffects([... pulledEffects, planar]);
    }, [track, planar]);

    return (
        <div ref={ref} className="quick-instrument">
          <div className="label">
            Instrument
          </div>
          <LockButton
            patternLocked={locked}
            toggleLock={() => track.togglePatternLock()}/>
          {!isFrozen &&
           <div
             onClick={() => isFrozen ? track.unfreeze() : freezePatterns(track)}
             className={isFrozen ? "pushable freeze-btn frozen" : "pushable freeze-btn"}>
             <img className="freeze-icon" src="/freeze-icon.svg"/> {isFrozen ? "Unfreeze" : "Freeze"}
           </div>}
          <div className="instrument-items">
          <div onClick={flip} className={show ? "pushable instrument-button show" : "pushable instrument-button"}>
            {track.instrument ? track.getName() :"Sampler"}
          </div>
            {track.sample && <WarpButton track={track}/>}
            {isWarped && <WarpKnob warp={getWarp(track)} track={track}/>}
          </div>
        </div>
    );
};

const getWarp = (track) => {
    return track.midiEffects.effects.find(x => x.name === "Warp");
};

const mapStateToProps = (state, props) => {
    let subSequenceId = props.track.subSequence.id;
    let locked = false;
    if (props.track !== undefined) {
        let subSequenceLocks = state.tracks.trackPatternLocks[props.track.subSequence.id] || {};
        let patterns = subSequenceLocks[props.track.trackNumber] || {};
        locked = patterns[props.track.currentPattern] || false;
    }
 
    return {
        locked,
        isFrozen: props.track.isFrozen,
        planar: state.effects.planar[props.track.id],
        show: state.spatial.showTrackInstrument,
        midiEffects: props.track.midiEffects.effects.length,
        isWarped: state.tracks.trackWarps[subSequenceId] &&
            state.tracks.trackWarps[subSequenceId][props.track.trackNumber]
  };
};

const mapDispatchToProps = (dispatch, props) => {
  return {
      setPulledEffects: m => dispatch(setPulledEffects(m)),
      setShow: x => dispatch(setShowTrackInstrument(x)),
      clearSelectedEffect: (type) => 
            dispatch(
                setSelectedMixEffect(
                    props.track.subSequence.id, props.track.id, undefined, type, undefined)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TrackQuickInstrument);
