import React, { useEffect, useCallback, useState} from 'react';
import {getPlugins} from 'plugins/plugins.js';
import {connect} from 'react-redux';
import PluginOption from './PluginOption.js';

const PluginsList = ({sequencerEngine}) => { 
    let [plugins, setPlugins] = useState([]);
    useEffect(() => {
        getPlugins().then(setPlugins);
    }, [setPlugins]);

    console.log("PLUGINs=", plugins);
    return (
        <div className="plugins-list">
          {plugins.map(plugin => <PluginOption plugin={plugin} sequencerEngine={sequencerEngine}/>)}
        </div>);
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PluginsList);
