import React, { useEffect, useCallback, useState} from 'react';
import {AUDIO} from 'constants/track-types.js';
import {
    SOUND_RATED,
    PRESET_RATED} from 'decentralized/xanadu/LinkTypes.js';
import SynthThumbnail from "components/v3/sidebar/SynthThumbnail.js";
import FavoriteButton from "components/FavoriteButton.js";
import {
    setSelectedZObject,
    setContextualHelp} from "actions/index.js";
import Documented from "components/help/Documented.js"
import {AUDITION_NEXT, AUDITION_PREVIOUS} from 'constants/contextual-help.js';
import {trim} from 'utils/trim.js';
import {freezePatterns} from 'components/v2/trackRightClickActions.js';
import TrackThumbnail from "components/TrackThumbnail.js";
import Colors from 'components/Colors.js';
import {connect} from 'react-redux';

const colors = new Colors();

const TrackInformation = ({track, isFrozen, setContextualHelp, setSelectedZObject}) => { 
    let style = {
    };
    if (!isFrozen) {
        style.backgroundColor = colors.getColorForTrack(track);
    }
    let className = "track-information";
    if (!track.sample) {
        className += " no-sampler-track";
    }
    if (track.getTrackType() === AUDIO) {
        className += " audio-track";
    }
    return (
        <div className={className}>
          <div className="thumb-and-browse">
            <div
              onMouseOver={() => setContextualHelp(AUDITION_PREVIOUS)}
              onMouseLeave={() => setContextualHelp(undefined)}
              onClick={() => track.auditionPrevious()} className="arrow-up"/>
            {track.instrument ? <SynthThumbnail track={track}/> :
             <TrackThumbnail
              onClickThumbnail={() => setSelectedZObject(track)}
               hasInner={true} track={track}/>}
            <div
              onMouseOver={() => setContextualHelp(AUDITION_NEXT)}
              onMouseLeave={() => setContextualHelp(undefined)}
              onClick={() => track.auditionNext()} className="arrow-down"/>
          </div>
          <div className="name-and-tags">
            <div className={!track.sample || !track.sample.tags ? "track-name instrument-name" : "track-name"}>{trim(track.name || track.presetName || track.getName())}</div>
            <div className="track-tags">
              {track.sample && track.sample.tags && track.sample.tags.map(
                  x => <div className="track-tag">{x}</div>)}
            </div>
          </div>
        {track.sample ? <FavoriteButton type={SOUND_RATED} contentHash={track.sample.getId()} /> : 
         track.preset && <FavoriteButton type={PRESET_RATED} contentHash={track.preset.contentHash}/>} 
       </div>);
}

const mapStateToProps = (state, props) => {
  return {
      id: props.track.id,
      name: props.track.presetName || props.track.name,
      isFrozen: props.track.isFrozen
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setContextualHelp: (element, arg) => dispatch(setContextualHelp(element, arg)),
      setSelectedZObject: (obj) => dispatch(setSelectedZObject(obj)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TrackInformation);
