import React, { useEffect, useCallback, useState} from 'react';
import ipfs from "decentralized/Ipfs.js";
import {connect} from 'react-redux';
import {
    setShowPlugins,
    setPluginsLoaded} from 'actions/index.js';
import ethereum from "decentralized/ethereum.js";

const PluginOption = ({
    setShowPlugins,
    sequencerEngine,
    plugin,
    setPluginsLoaded
}) => { 
    let [description, setDescription] = useState('');
    let [thumbnail, setThumbnail] = useState(undefined);
    let [creator, setCreator] = useState('');

    useEffect(() => {
        ipfs.getJson(plugin.versions[0].pluginMetadataURI).then(
            json => {
                setDescription(json.description);
                setThumbnail(json.image);
            });

        ethereum.users.getUserName(plugin.creator).then(setCreator);
    }, [setCreator, plugin, setDescription, setThumbnail]);

    const loadPlugin = useCallback(() => {
        ipfs.getJson(plugin.versions[0].pluginContentHash).then(
            json => {
                let o = sequencerEngine.currentSubSequence.createZObject(5,5,"subpatch");
                o.hide();
                o.loadPresetJson(json, false, true, true);
                o.plugin = plugin;
                
                let pluginsLoaded = sequencerEngine.state.plugins.pluginsLoaded || [];
                setPluginsLoaded([... pluginsLoaded, o]);
                setShowPlugins(false);
            });
    }, [plugin]);
    return (
        <div className="plugin-option">
          <div className="plugin-name">
            {thumbnail && <img src={thumbnail}/>}
            <div className="name-and-author">
              <div className="name-text">{plugin.name}</div>
              <div className="creator-text">{creator}</div>
            </div>
          </div>
          <div className="plugin-description">{description}</div>
          <div
            onClick={loadPlugin}
            className="load-button pushable">
            Load Plugin
          </div>
        </div>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setPluginsLoaded: x => dispatch(setPluginsLoaded(x)),
      setShowPlugins: m => dispatch(setShowPlugins(m))
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PluginOption);
