import React, { useEffect, useCallback, useState} from 'react';
import { ButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';
import {connect} from 'react-redux';
import {
    setFullScreenZObject} from 'actions/index.js';

const SubPatchMenu = ({
    setFullScreenZObject,
    setEditLayout,
    editLayout,
    setCustomMode,
    customMode,
    subpatch,
    presentationMode,
}) => { 
    let [toggled, setToggled] = useState(false);

    useEffect(() => {
        if (!customMode) {
            return;
        }
        if (presentationMode) {
            setEditLayout(true);
        } else {
            setEditLayout(false);
        }
    }, [presentationMode, customMode]);
    
    const toggleCustom = useCallback(() => {
        setEditLayout(!customMode);
        subpatch.isCustom = !customMode;
        setCustomMode(!customMode);
    }, [customMode, setCustomMode, subpatch, setEditLayout]);

    return (
        <ButtonDropdown
        className="subpatch-menu"
        show={toggled ? "true" : "false"}
        isOpen={toggled}
          toggle={() => setToggled(!toggled)}>
        <DropdownToggle size="sm" caret color="primary">
          Edit
        </DropdownToggle>
        <DropdownMenu>
          {customMode && 
          <DropdownItem
            onClick={() => setFullScreenZObject(subpatch.zObject)}
           >
            Full Screen
          </DropdownItem>}
          <DropdownItem
            key=""
            onClick={toggleCustom}
            size="sm">
            Instrument Mode
            {customMode &&
             <span className="small_checkmark">
               <div className="small_checkmark_stem"></div>
               <div className="small_checkmark_kick"></div>
             </span>}
          </DropdownItem>
        {customMode && <DropdownItem
                         key=""
                         onClick={() => setEditLayout(!editLayout)}
                         size="sm">
                         Edit Layout
                         {editLayout &&
                          <span className="small_checkmark">
                            <div className="small_checkmark_stem"></div>
                            <div className="small_checkmark_kick"></div>
                          </span>}
                       </DropdownItem>}
        </DropdownMenu>
        </ButtonDropdown>
    );
};

const mapStateToProps = (state, props) => {
  return {
      presentationMode: state.spatial.presentationMode
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setFullScreenZObject: x => dispatch(setFullScreenZObject(x)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubPatchMenu);
