import React, { useEffect, useCallback, useState} from 'react';
import ObjectTree from './ObjectTree.js';
import {connect} from 'react-redux';

const SubPatchObjectSidebar = ({subpatch}) => { 
    let [opened, setOpened ] = useState(false);
    let className = "subpatch-left-sidebar";
    if (opened) {
        className += " opened"
    }
    return (<div className={className}>
              <img
                onClick={() => setOpened(!opened)}
                src="/tree-icon.svg" className="pushable tree-icon"/>
            {opened &&
             <ObjectTree subpatch={subpatch}/>}
            </div>);
};

const mapStateToProps = (state, props) => {
  return {
      height: props.subpatch.height
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SubPatchObjectSidebar);
