import React, { useEffect, useCallback, useState} from 'react';
import AttributesInspector from './AttributesInspector.js';
import {connect} from 'react-redux';
import ZNumberInspector from "components/sidebar/ZNumberInspector.js"
import {BANG, LIST, SYMBOL, NUMBER} from 'zobject/data-types.js';
import './zmessage.scss';

const ZMessageTools = ({zmessage}) => { 
    let tools = []
    if (zmessage.type === NUMBER) {
        tools.push(<ZNumberInspector key={1} znumber={zmessage}/>);
    } 

    tools.push(<AttributesInspector zobject={zmessage}/>);

    return tools;
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZMessageTools);
