import React, { useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {SIZE} from './constants.js';

const AutomationPaths = ({automationPaths}) => { 
    const getPath = (path) => {
        return "M " + SIZE*path[0].x + ','+ SIZE*path[0].y + " L " +
            path.slice(1).map(({x,y}) => x*SIZE +','+ y*SIZE).join(',');
        
    };
    return automationPaths.map(
        _path =>
            <path fill="transparent" d={getPath(_path)} strokeWidth={2} stroke="red"/>);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AutomationPaths);
