import React, { useEffect, useCallback, useState} from 'react';
import PadPacks from './PadPacks.js';
import ZNumberInspector from './ZNumberInspector.js';
import {NUMBER} from "zobject/data-types.js";
import PresetsExplorer from "components/installation/PresetsExplorer.js";
import {connect} from 'react-redux';
import {
    setTrackPresetSaving} from 'actions/index.js';

const ZObjectSidebar = ({zobject, setTrackPresetSaving, searchText}) => { 
    const savePreset = useCallback(() => {
        setTrackPresetSaving(zobject);
    }, [zobject]);

    let {numInlets, numOutlets} = zobject;
    if (zobject.effect) {
        numInlets += zobject.effect.numInlets;
        numOutlets += zobject.effect.numOutlets;
    }
    return (
        <div className="zobject-sidebar">
          {zobject.type === NUMBER &&
           <div className="z-object-inspector">
             <div className="z-object-name">
               <div className="z-object-thumb"/><span className="z-name">Number</span>
             </div>
             <ZNumberInspector znumber={zobject}/>
           </div>}
          
          {zobject.function &&
           <div className="z-object-inspector">
             <div className="z-object-name">
               <div className="z-object-thumb"/><span className="z-name">{zobject.name}</span>
             </div>
             <div className="z-object-info">
               Inlets: {numInlets}
             </div>
             <div className="z-object-info">
               Outlets: {numOutlets}
             </div>
             <img
               onClick={savePreset}
               src="/floppy-disk.svg" className="save-preset-disk"/>
           </div>}

        {zobject.isPad && <PadPacks pad={zobject}/>}
          {!zobject.isPad && zobject.type !== NUMBER &&
          <PresetsExplorer
            initialSize={18}
            zobject={zobject}
            content={zobject.preset}
            searchText={searchText}
            type={zobject.getInstrumentType()}/>}
        </div>
    );
};

const mapStateToProps = (state, props) => {
  return {
      searchText: state.selector.quickAddSearchText,
      preset: props.zobject && props.zobject.preset,
      name: props.zobject && props.zobject.name
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      setTrackPresetSaving: x => dispatch(setTrackPresetSaving(x))     
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ZObjectSidebar);
