import React, {useState, useEffect, useCallback} from 'react';

export const useWindowSlider = ({min, max, setValue, value}) => {
    let [editing, setEditing] = useState(false);

    useEffect(() => {
        window.addEventListener('mousemove', onMouseMove);
        window.addEventListener('mouseup', onMouseUp);

        return () => {
            window.removeEventListener('mousemove', onMouseMove);
            window.removeEventListener('mouseup', onMouseUp);
        };
    }, [editing, min, max, setValue, value]);

    const onMouseMove = useCallback((e) => {
        if (editing) {
            console.log('editing');
            let {y, number} = editing;
            let {pageY} = e;
            let distY = y - pageY;
            let ratio = Math.pow(distY / y, 2);
            if (distY < 0) {
                ratio *= -1;
            }
            let _number = ratio*(max - (min+0.001)) + number;
            console.log("Number = ", _number);
            _number = Math.max(min, Math.min(max, _number));
            if (_number !== value) {
                setValue(_number);
            }
        }
    }, [min, editing, max, value, setValue]);

    const onMouseUp = useCallback(() => {
        setEditing(false);
    }, [setEditing]);

    const onMouseDown = useCallback((e) => {
        setEditing({number: value, y: e.pageY});
    }, [setEditing, value]);

    return {
        editing,
        onMouseDown
    };
    
};
