import React, { useRef, useEffect, useCallback, useState} from 'react';
import {getInstrumentComponent} from "components/effects/getInstrumentComponent.js";
import {connect} from 'react-redux';
import DetailsView from "components/DetailsView.js"

const InstrumentViewer = ({track, containerRef}) => { 
    let wrapperRef = useRef();
    /*
    if (track.isFrozen) {
        return '';
    }
    */
    if (track.instrument && !track.isFrozen) {
        return (
            <div className="instrument-wrapper">
              {getInstrumentComponent(track, containerRef)}
            </div>);
    } else {
        // its a sampler so show the sampler
        return (<div  className="sampler-wrapper">
                  <div ref={wrapperRef} className="waveform-inner">
                    <DetailsView.DetailsView
                      width={window.innerWidth - 240*2-60}
                      backgroundColor="transparent"
                      div={wrapperRef}
                      small={false}
                      showParameters={false}
                      hideKeyboard={true}
                      alreadyLoaded={true}
                      onGoBack={() => []}
                      keyboardEngine={track.sequencerEngine.keyboardEngine}
                      midiEngine={track.sequencerEngine.midi}
                      trackNumber={track.trackNumber}
                      resolution={track.sequencerEngine.resolution}
                      height={180}
                      sequencerEngine={track.sequencerEngine}
                      bpm={track.sequencerEngine.bpm}
                      track={track}/>
                  </div>
                </div>);
    }
}

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(InstrumentViewer);
