import React, {useCallback, useEffect} from 'react';
import {
    setEdgesPadding,
    setEdgesSize,
    setEditorPadding,
    setZObjectSize,
    setSelectedPulledElements,
    setBulkWindowPositions,
    setWindowPosition} from 'actions/index.js';
import {
    getPositions,
    doesElementIntersect,
    getMinLeftAndTop,
    getMaxLeftAndTop} from './utils.js';

export const useEdgeAdjuster = (elements, subSequence, sequencerEngine, sizeRef, setSize) => {
    const adjust = () => {
        let size = sizeRef.current;
        let {minTop, minLeft} = getMinLeftAndTop(elements);
        let {maxTop, maxLeft} = getMaxLeftAndTop(elements);
        
        minTop -= 100;
        minLeft -= 100;
        
        if (sequencerEngine) {
            let {edgesSize, edgesPadding} = sequencerEngine.state.spatial;
            
            let newSize = {
                width: maxLeft - minLeft,
                height: maxTop  - minTop
            };
            
            if (newSize.width !== edgesSize.width ||
                newSize.height !== edgesSize.height) {
                sequencerEngine.dispatch(
                    setEdgesSize(newSize.width + 300, newSize.height + 300));
            }
            
            if ((edgesPadding.x !== minLeft || edgesPadding.y !== minTop)) {
                sequencerEngine.dispatch(
                    setEdgesPadding(minLeft,
                                    minTop));
            }
        }
        
        if (maxLeft < size.width ||
            maxTop < size.height) {
            if (!(maxLeft > size.width || maxTop > size.height)) {
                return;
            }
            let _size = {
                width: Math.max(size.width, maxLeft),
                height: Math.max(size.height, maxTop),
            };
            sizeRef.current = _size;
            setSize(_size);
        } 
    };

    useEffect(() => {
        setTimeout(() => {
            adjust();
        }, 300);
    }, []);
};
