import React, {useEffect, useCallback} from 'react';
export const useKeys = ({selected, setSelected, higherOrderSequencer}) => {
    useEffect(() => {
        window.addEventListener("keydown", onKeyDown);
        return () => {
            window.removeEventListener("keydown", onKeyDown);
        };
    }, [, selected, setSelected]);

    const onKeyDown = useCallback((e) => {
        if (e.target.tagName === 'INPUT' ||
            e.target.tagName === 'TEXTAREA') {
            return;
        }

        if (e.key === 'Backspace') {
            e.preventDefault();
            if (selected && selected.length > 0) {
                higherOrderSequencer.deleteSteps(selected);
                setSelected(undefined);
            }
        } else {
            if (!selected) {
                return;
            }
            let x=0;
            let y=0;
            if (e.key === 'Enter') {
                e.preventDefault();
                selected.forEach(
                    ({x,y}) => 
                        higherOrderSequencer.toggle(x,y));
                return;
            }
            if (e.key === 'ArrowLeft') {
                e.preventDefault();
                x--;
            } else if (e.key === 'ArrowUp') {
                e.preventDefault();
                y--;
            } else if (e.key === 'ArrowRight') {
                e.preventDefault();
                x++;
            } else if (e.key === 'ArrowDown') {
                e.preventDefault();
                y++;
            }
            selected.forEach(
                pt => higherOrderSequencer.moveStepTo(
                    pt.x, pt.y, pt.x+x, pt.y+y));
        }
    }, [selected, setSelected]);
};
